<template>
  <div @dblclick="openDetail(content)" @click="setSelected" class="cover-wrapper" style="position: relative"
       :style="{width:itemSize+'px',height:itemSize+'px'}">
    <div class="type-label">{{ typeAlias }}</div>
    <a-image :style="{width:itemSize+'px',height:itemSize+'px',objectFit:'cover',padding:itemSize*0.2+'px'}"
             :preview="false" style="background: white; "
             class="sub-folder-cover content-cover"
             :src="contentCover(content)">
    </a-image>

<!--    <div class=" type-label preview" @click.stop="">-->
<!--      <zoom-in-outlined/>-->
<!--    </div>-->
  </div>
  <div class="folder-name">{{ content.name }}</div>
  <div class="ext"><span>{{ content.ext.substr(content.ext.lastIndexOf('.') + 1) }}</span>
    {{ tools.convertToText(content.baseInfo.size) }}
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
// import { ZoomInOutlined } from '@ant-design/icons-vue'
import contentTypeHelper from '@/utils/contentTypeHelper'
import tools from '@/utils/tools'

export default {
  name: 'TypeOther',
  emits: ['setSelected', 'openDetail'],
  components: {
    // ZoomInOutlined
  },
  props: {
    content: Object
  },
  computed: {

    ...mapState(
        {
          itemSize: state => state.ui.itemSize
        }
    ),
    typeAlias () {
      try {
        return contentTypeHelper.getContentTypeAliasFromExt(this.content.ext)
      } catch (e) {
        return ''
      }
    }
  },
  data () {
    return {
      tools
    }
  },
  methods: {
    getExtNoDotFromFilename: contentTypeHelper.getExtNoDotFromFilename,
    contentCover (content) {
      return  contentTypeHelper.getContentCover(content.ext)
    },
    openDetail (content) {
      this.$emit('openDetail', content)
    },
    ...mapMutations({
      focusItem: 'ui/focusItem'
    }),
    setSelected () {
      this.$emit('setSelected')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

