import pathTools from "@/utils/pathTools";
import contentModel from '@/utils/model/contentModel'

const electron = eval('require')('electron')
const shell=eval('require')('electron').shell
const ipcSender = electron.ipcRenderer
const fs=eval('require')('fs')
const path=eval('require')('path')
const ipc = {
  /**
   * 
   * @param fullPath
   */
  openExternal(fullPath){
    ipcSender.send('openExternal', { fullPath })
  },
  /**
   * 
   * @param dir
   */
  openDir (dir) {
    ipcSender.send('openDir', { dir })
  },
  /**
   * 
   * @param fullPath
   */
  showItemInFolder (fullPath) {
    ipcSender.send('showItemInFolder', { fullPath })
  },
  /**
   * 
   * @param type
   * @param args
   */
  handleFileAssign (type,args) {
    ipcSender.send('handleFileAssign', { type: type, args: { filePath: args.filePath } })
  },
  /**
   * 
   * @param wallPaper
   */
  setWallPaper(wallPaper){
    ipcSender.send('setWallPaper',{wallPaper:wallPaper})
  },
  /**
   * 
   * @param fullPath
   */
   trashItem(fullPath){
    let filePath = fullPath
     shell.trashItem(filePath).then(()=>{
      contentModel.removeContent(fullPath)
     })

   // ipcSender.send('trashItem', { fullPath })
  },

  trashFolder(fullPath){
     //todo 
     return fullPath
    // let filePath = fullPath
    // if (process.platform === 'win32') {
    //   filePath = filePath.replaceAll('/', '\\')
    // }
    // shell.trashItem(filePath).then(()=>{
    //   contentModel.removeContent(fullPath)
    // })
  },
  /**
   * 
   * @param dirPath
   * @param dirName
   * @param $messager
   */
  createDir(dirPath,dirName,$messager){
    if(!dirName || dirName.trim()===''){
      $messager.error('')
      return
    }
    dirName=dirName.replaceAll('/','').replaceAll('\\','')
    if(dirName==='' || dirName.trim()===''){
      $messager.error('')
      return
    }
    let createDir=path.join(dirPath,dirName).replaceAll('\\','/')
    if(fs.existsSync(createDir)){
      $messager.error('')
    }else{
      fs.mkdirSync(createDir)
      if(fs.existsSync(createDir)){
        window.libDb.addFolderMetaInfo(pathTools.getRelativeFullPathFromFullPath(createDir))
        window.app.$store.dispatch('subFolder/updateTreeData')
        window.app.$store.dispatch('ui/reload')
        $messager.success(''+dirName+'')
      }
    }
    // if(fs.existsSync())
    //
    // fs.mkdirSync(path.join(path,dirName).replaceAll('\\','/'))
    //
    //
    ipc.once('createDirResult',(event,args)=>{
      if(args.result==='true'){
        $messager.success('')
        window.app.$store.dispatch('subFolder/updateTreeData')
      }else{
        $messager.error(''+args.message)
      }
    })

   // ipcSender.send('createDir',{path, dirName})
  },
  renameFolder(oldName,newName){
    if(newName===''){
      window.app.$message.error({content:
          ''})
      return
    }
    const oldPathParent=oldName.substr(0,oldName.lastIndexOf('/'))
    const oldPath=path.join(window.$defaultStrorePath,oldName)
    const newPath=path.join(window.$defaultStrorePath,oldPathParent,newName)
    if(fs.existsSync(newPath)){
      window.app.$message.error('')
    }else{
      fs.renameSync(oldPath,newPath)
      window.libDb.renameMetaFolder(pathTools.convertToCommonRelativeDir(oldName),pathTools.convertToCommonRelativeDir(path.join(oldPathParent,newName)))
      if(fs.existsSync(newPath)){
        //todo meta
        //todo meta
        window.app.$message.success('')
        window.app.$store.dispatch('subFolder/updateTreeData')
        window.app.$store.dispatch('ui/reload')
      }else{
        window.app.$message.error('')
      }
    }

  },
  /**
   * 
   * @returns {*}
   */
  fetchAllPwds(){
    return ipcSender.invoke('credentialStoreGetCredentials')
  },
  bindAddPageInfoListener(callback,callbackCapture){
    ipcSender.on('gotAddPageInfo',(event,args)=>{
      callback(args)
    })
    ipcSender.on('gotHdCaptureData',(event,args)=>{
      callbackCapture(args)
    })

  },
  /**
   * 
   */
  getAddPageInfo(){
    ipcSender.send('getAddPageInfo')
  },
  /**
   * 
   */
  noticeAddPageReady(){
    ipcSender.send('addPageReady')
  },
  hideFavPop(){
    ipcSender.send('hideFavPop')
  },
  /**
   * 
   * @param url
   */
  addTab(url){
    ipcSender.send('addTab',{url:url})
  },
  message(args){
    ipcSender.send('message',args)
  },
  /**
   * ipc
   * @param channel
   * @param func
   */
  once(channel,func){
    ipcSender.once(channel,func)
  },
  /**
   * 
   */
  callAppReload(){
    ipcSender.send('reloadFav')
  }
}
/**
 * 
 */
ipcSender.on('addImageMeta',(event,args)=>{
  let storePath=pathTools.getRelativeFullPathFromFullPath(args.storePath)
  let img=window.libDb.addImgMetaFromFile(storePath)
  contentModel.update(img.uuid,{
    href:args.href
  })
  window.app.$store.dispatch('ui/reload')
})
ipcSender.on('error',(event,args)=>{
  window.app.$message.error(args.message)
})
ipcSender.on('success',(event,args)=>{
  window.app.$message.success(args.message)
})
/**
 * 
 */
ipcSender.on('reload',()=>{
  window.app.$store.dispatch('ui/reload')
})

export default ipc
