<template>
  <div class="hello">
    <h3 style="margin-bottom:10px;margin-top: 20px">{{ contentsOrderByConfig.length }}</h3>
    <div style="position: fixed;display: inline-block;width: 30px;height: 30px;text-align: center">

    </div>

    <div id="itemsLists" style="min-height:150px">
      <a-empty style="margin-top: 60px" description="" v-if="contentsOrderByConfig.length===0"></a-empty>
      <a-dropdown v-else :trigger="['contextmenu']" v-for="content in contentsOrderByConfig" v-bind:key="content">
        <div class="content-wrapper" @mousedown.stop="()=>{}" @mouseup="dragging=false" :uuid="content.meta.uuid"
             draggable="true"
             @dragstart="onDragStart($event,content)"
             @dragend="onDragEnd"
             @dragover="onDragOver"
             :class="{'selected':!!this.focusItem.data && this.focusItem.data.meta.uuid===content.meta.uuid}"
             @click="selectedKey=content"

             :style="{width:itemSize+'px',height:itemSize+50+'px'}">
          <template v-if="content.extCanOpenInside">
            <TypePwd @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='pwd'"
                     v-bind:content="content"/>
            <TypeImg @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='img'"
                     v-bind:content="content"/>
            <TypeUrl @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='url'"
                     v-bind:content="content"/>
            <TypeVideo @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='video'"
                       v-bind:content="content"/>
          </template>
          <template v-else>
            <TypeOther @setSelected="selectItem(content)" @openDetail="openPath" v-bind:content="content"/>
          </template>
          <div v-if="content.meta.new" class="type-label new-label">New</div>
        </div>
        <template #overlay>
          <a-menu>
            <a-menu-item key="openPath" @click="open(content)"></a-menu-item>
            <a-menu-item key="find" @click="showItemInFolder(content.baseInfo.path+'/'+content.baseInfo.filename)">
            </a-menu-item>
            <a-menu-item key="delete" @click="trashItem(content)">
            </a-menu-item>
            <a-menu-item v-if="content.type==='img'" key="edit"
                         @click="editPicture(content.baseInfo.path+'/'+content.baseInfo.filename)">
            </a-menu-item>
            <a-menu-item v-if="content.type==='img'" key="setWallpaper"
                         @click="setNewTabWallPaper(content.baseInfo.path+'/'+content.baseInfo.filename)">
            </a-menu-item>
            <a-menu-item v-if="content.type==='img'" key="setDesktopWallPaper"
                         @click="setDeskWallpaper(content.baseInfo.path+'/'+content.baseInfo.filename)">
            </a-menu-item>
          </a-menu>
        </template>
      </a-dropdown>
    </div>
  </div>
  <VueSelecto
      dragContainer=".ant-layout-content"
      :selectableTargets='[".content-wrapper"]'
      :selectByClick="true"
      :selectFromInside="true"
      :continueSelect="false"
      :toggleContinueSelect='"shift"'
      :hitRate="0"
      @select="onSelect"
  />
</template>
<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import TypePwd from '@/components/content/type/TypePwd'
import TypeImg from '@/components/content/type/TypeImg'
import { Modal } from 'ant-design-vue'
import ipc from '@/utils/ipc'
import TypeOther from '@/components/content/type/TypeOther'
import TypeVideo from '@/components/content/type/TypeVideo'
import TypeUrl from '@/components/content/type/TypeUrl'
import { VueSelecto } from 'vue3-selecto'
import contentModel from '@/utils/model/contentModel'
import pathTools from '@/utils/pathTools'


const wallpaper = eval('require')('wallpaper')
export default {
  name: 'ContentItemList',
  components: { TypePwd, TypeImg, TypeOther, TypeVideo, TypeUrl, VueSelecto },
  props: {},
  data () {
    return {
      selectedKey: null,
      dragging: false,
      left: 0,
      top: 0,
      timer: null
    }
  },
  mounted () {
  },

  methods: {
    setDeskWallpaper (str) {
      this.$message.success('')
      wallpaper.set(str)
    },
    onDragOver () {
    },
    async onDragStart (event, content) {
      if (this.$store.state.ui.selectedItems.length <= 1) {
        this.selectItem(content)
        return
      }
      this.dragging = true
      let pic = document.getElementById('selectedItemsCover')
      event.dataTransfer.setDragImage(document.getElementById('selectedItemsCover'), pic.offsetWidth / 2, pic.offsetHeight / 2)
    },
    onDragEnd () {
      this.dragging = false
    },
    onSelect (e) {
      // if(e.selected.length===0){
      //   return
      // }
      // let all=document.getElementsByClassName('selected')
      // for(let i=0;i<all.length;i++){
      //   all[i].classList.remove('selected')
      // }
      // this.$store.commit('ui/clearFocusItem')
      e.added.forEach(el => {
        el.classList.add('selected')
      })
      e.removed.forEach(el => {
        el.classList.remove('selected')
      })
      if (e.selected.length === 1) {
        this.$store.commit('ui/focusItem', {
          type: 'content',
          data: contentModel.getContentFromUuid(e.selected[0].attributes['uuid'].value)
        })
        return
      }

      let selectedUuids = []
      e.selected.forEach(sel => {
        selectedUuids.push(sel.attributes['uuid'].value)
      })

      this.$store.commit('ui/setSelectedItemsByUuids', selectedUuids)
    },
    ...mapMutations({
      'unshiftRecent': 'content/unshiftRecent'
    }),
    open(content){
      if(content.type === 'url'){
        ipc.addTab(content.meta.href)
      }else{
        ipc.openExternal(pathTools.join(content.baseInfo.path,content.baseInfo.filename))
      }
      this.unshiftRecent(content.meta.uuid)
    },
    showItemInFolder: ipc.showItemInFolder,
    selectItem (content) {
      console.log('change',content)
      this.$store.commit('ui/focusItem', {
        type: 'content',
        data: content
      })
      this.$store.commit('ui/setSelectedItemsByUuids', [content.meta.uuid])
    },
    openPath (content) {
      if (content.type === 'url') {
        ipc.addTab(content.meta.href)
      }else if(content.ext === '.pdf'){
        let base = 'file://' + window.globalArgs['app-path'] + '/pages/pdfViewer/index.html'
        let queryString = '?url=' + encodeURI(content.baseInfo.path + '/' + content.baseInfo.filename)
        ipc.addTab(base + queryString)
      } else {
        ipc.openExternal(content.baseInfo.path + '/' + content.baseInfo.filename)
      }
      this.unshiftRecent(content.meta.uuid)
    },
    openDetail (content) {
      if (content.type === 'img') {
        this.$router.push({ path: '/main/img/' + encodeURIComponent(content.baseInfo.path + '/' + content.baseInfo.filename) })
      }else if(content.type==='video'){
        this.$router.push({ path: '/main/video/' + encodeURIComponent(content.baseInfo.path + '/' + content.baseInfo.filename) })
      } else if (content.type === 'url') {
        ipc.addTab(content.meta.href)
      } else {
        ipc.openExternal(pathTools.join(content.baseInfo.path,content.baseInfo.filename))
        return
      }
      this.unshiftRecent(content.meta.uuid)
    },
    trashItem (content) {
      let path=pathTools.join(content.baseInfo.path,content.baseInfo.filename)
      Modal.confirm({
        title: '',
        content: '',
        okText: '',
        cancelText: '',
        onOk: () => {
          contentModel.removeContent(path)
          window.app.$store.dispatch('subFolder/updateTreeData')
          window.app.$store.dispatch('ui/reload')
          this.$message.success('')
        }
      })
    },
    //
    setNewTabWallPaper (wallPaper) {
      ipc.setWallPaper(wallPaper)
      this.$message.success('')
    },
    //ipc
    editPicture (src) {
      ipc.handleFileAssign('image', { filePath: 'file://' + src })
    }
  },
  computed: {
    ...mapState(
        {
          subFolders: state => state.subFolder.subFolders,
          itemSize: state => state.ui.itemSize,
          contents: state => state.content.contents,
          focusItem: state => state.ui.focusedItem,
          selectedItems: state => state.ui.selectedItems
        }
    ),
    ...mapGetters('content',
        ['contentsOrderByConfig']
    )
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
