// 1.0.0

import pathTools from "@/utils/pathTools";

const low = eval('require')('lowdb')
const FileSync = eval('require')('lowdb/adapters/FileSync')
const fs = eval('require')('fs')
//
import {v4} from 'uuid'
import fileHelper from "@/utils/fileHelper";
import contentTypeHelper from "@/utils/contentTypeHelper";
import pathTool from '@/utils/pathTools'

let db = null
let libDb = {
    dbInstance: null,
    libPath: '',
    assetsPath: '_assets',
    /**
     * 
     * @returns {string}
     */
    getAssetsRelativePath() {
        return libDb.assetsPath
    },
    /**
     * 
     * @returns {string}
     */
    getAssetsPath() {
        let assetPath = libDb.libPath + '/' + libDb.assetsPath
        if (!fs.existsSync(assetPath)) {
            fs.mkdirSync(assetPath)
        }
        return assetPath.replaceAll('//', '/')
    },
    updateMeta(uuid,meta){
      libDb.reload()
      db.get('content_meta').find({uuid:uuid}).assign(meta).write()
    },
    /**
     * 
     * @param libPath
     */
    init(libPath) {
        libDb.libPath = libPath
        const adapter = new FileSync(libDb.libPath + '/_db.json')
        const dbAdapter = low(adapter)
        libDb.dbInstance = dbAdapter
        libDb.dbInstance.defaults({
            content_meta: [
                /**
                 {
            folder:xxx,
            folder_uuid:xxx,
            uuid:xxx,
            //extCanOpenInside:contentTypeHelper.extCanOpenInside(fileExt),
            name: file.filename,
            type: type,
            ext: fileExt,
            cover: 'file://' + file.path + filename,
            extData: {
              // width: '900',
              // height: '420'
            },
            baseInfo: {
              path: file.path,
              relativePath:relativePath,
              filename: filename,
              createTime: fileInfo.birthtimeMs,
              addTime: fileInfo.ctimeMs,
              modifyTime: fileInfo.mtimeMs,
              size: fileInfo.size,
              sizeText: tools.convertToText(fileInfo.size)
            },
            href: ''
          }
                 */
            ],//
        }).write()

        db = libDb.dbInstance
        libDb.repairDb()
    },
    repairDb(){
      db.get('content_meta').remove({name:''}).write()
      db.set('content_meta',db.get('content_meta').uniqBy('relative_path_full').value()).write() //
    },
    /**
     * Meta
     * @param relativePathFull
     * @returns {{}} {}name
     */
    getMeta(relativePathFull) {
        libDb.reload()
        let meta = db.get('content_meta').find({relative_path_full: pathTools.convertToCommonRelativePath(relativePathFull)}).value()
        if (!meta) meta = {}
        return meta
        //db.get('content_meta').find({})
    },
    getMetaFromUuid(uuid){
        libDb.reload()
       let meta= db.get('content_meta').find({uuid: uuid}).value()
        return meta || null
    },
    /**
     * 
     */
    reload(){
        //todo 
       // libDb.init(libDb.libPath)
    },
    hardReload(){
        console.log('')
      libDb.init(libDb.libPath)
    },
    /**
     * meta
     * @param folder
     * @param filename
     * @param metaInfo
     * @returns {*}
     */
    addMeta(folder, filename, metaInfo) {
        //let stat = fs.statSync((window.configDb.getStorePath() + '/' + folder + '/' + filename).replaceAll('//', '/'))
        let contentMeta = Object.assign({
            folder: folder,
            folder_uuid: '',
            uuid: v4(),
            relative_path: pathTools.convertToCommonRelativePath(folder),
            relative_path_full: pathTools.convertToCommonRelativePath(folder + '/' + filename),
            base_info: {
                update_time: Date.now(),//mtimemeta
            }

        }, metaInfo)
        db.get('content_meta').push(contentMeta).write()
        return contentMeta
    },
    /**
     * Urlmeta
     * @param folder
     * @param title
     * @param url
     * @param cover
     * @param favicon
     * @param remark
     * @param rate
     * @returns {*}
     */
    addUrl(folder, title, url, cover, favicon, remark,rate) {
        const ext = '.url'
        const uuid = v4()
        let metaInfo = {
            type: 'url',
            remark: remark,
            name: title,
            uuid: uuid,
            href: url,
            rate:rate,
            new: true,//
            ext_data: {
                favicon: favicon
            }
        }
        let urlTpl = `
[DEFAULT]
[InternetShortcut]
URL=${url}
`
        fs.cpSync(cover.replace('file://', ''), libDb.getAssetsPath() + '/' + uuid + '.jpg')//
        metaInfo.cover = '/' + libDb.getAssetsRelativePath() + '/' + uuid + '.jpg'
        fs.writeFileSync((window.configDb.getStorePath() + '/' + folder + '/' + title + ext).replaceAll('//', '/'), urlTpl, 'utf-8')//
        return libDb.addMeta(folder, title + ext, metaInfo)
    },
    /**
     * MetaInfo
     * @param relativeFullPath
     * @returns {*}
     */
    addImgMetaFromFile(relativeFullPath) {
        return libDb.addCommonMetaInfo(relativeFullPath)
    },
    /**
     * MetaInfo
     * @param relativeFullPath
     * @returns {*}
     */
    addVideoMetaFromFile(relativeFullPath) {
        return libDb.addCommonMetaInfo(relativeFullPath)
    },
    /**
     * commondirwindows
     * @param dir
     */
    removeDirMeta(dir){
        let removedCount=0
        let relativePath= pathTool.getRelativeFullPathFromFullPath(dir)
        db.get('content_meta').remove({relative_path_full:relativePath}).write()
        removedCount=1
        relativePath=pathTool.appendEndsSplash(relativePath)

        db.get('content_meta').remove((meta)=>{
            if(meta.relative_path_full.startsWith(relativePath)){
                removedCount++
                return true
            }else{
                return false
            }
        }).write()
        return removedCount
    },
    /**
     * meta
     * @param relativeFullPath
     * @returns {*}
     */
    getFolderMetas(relativeFullPath){
        let path=pathTools.appendEndsSplash(relativeFullPath)
        let result= db.get('content_meta').filter((meta)=>{
            if(meta.relative_path_full.startsWith(path)){
                return true
            }else{
                return false
            }
        }).value()
        return result
    },
    removeMeta(relativePathFull){
        //let stat = fs.statSync((window.configDb.getStorePath() + '/' + folder + '/' + filename).replaceAll('//', '/'))
        libDb.reload()
        db.get('content_meta').remove({relative_path_full:relativePathFull}).write()
    },
    /**
     * metaInfo
     * @param relativeFullPath
     * @param metaInfo metaInfo
     * @returns {{new: boolean, ext_data: {}, name: string, remark: string, base_info: {update_time: number}, href: string, type: (string|*), uuid: (*|string)}}
     */
    addCommonMetaInfo(relativeFullPath, metaInfo) {
        const uuid = v4()
        let all=pathTools.getAllFromRelativeFullPath(relativeFullPath)
        let folder =all.relativeFolder
        let filename =all.filename
        let name = all.name
        metaInfo = Object.assign({
            type: contentTypeHelper.getContentTypeFromFilename(filename).name,
            remark: '',
            name: name,
            uuid: uuid,
            href: '',
            new: true,//
            ext_data: {},
            base_info: {
                update_time: Date.now()
            }
        }, metaInfo)
        return libDb.addMeta(folder, filename, metaInfo)
    },
    addUrlMetaInfo(relativeFullPath){
       //href
        try{
            let all=pathTools.getAllFromRelativeFullPath(relativeFullPath)
            let pattern=`[a-zA-z]+://[^\\s]*`
            let urlText=String(fs.readFileSync(all.storeFullPath))
            let url=urlText.match(pattern)
            if(url.length>0){
                url=url[0]
            }
            let metaInfo = {
                type: 'url',
                remark: '',
                name: all.name,
                href: url,
                new: true,//
                ext_data: {
                    favicon: ''
                },
                cover:''
            }
            return libDb.addCommonMetaInfo(relativeFullPath,metaInfo)
        }catch (e) {
            console.warn(e)
        }
    },
    addFolderMetaInfo(relativeFullPath){
        let metaInfo={
            name:relativeFullPath.substr(relativeFullPath.lastIndexOf('/')+1)
        }
       return libDb.addCommonMetaInfo(relativeFullPath,metaInfo)
    },
    /**
     * metameta
     */
    scanLib(lib=libDb.libPath) {
        let newFiles = []
        fileHelper.getFileTree(lib, {
            showFile: false, mapCallback: (dir, file) => {
                let relativePath = pathTools.join(pathTools.getRelativeFullPathFromFullPath(dir),file)
                let meta = libDb.getMeta(relativePath)
                if (!meta.name) {
                    newFiles.push(relativePath)
                    //todo meta
                    let type = contentTypeHelper.getContentTypeFromFilename(relativePath)
                    //let ext = contentTypeHelper.getExtNoDotFromFilename(relativePath)
                    let meta={}
                    switch (type.name) {
                        case  'img':
                            meta=libDb.addImgMetaFromFile(relativePath)
                            break
                        case 'folder':
                            meta=libDb.addFolderMetaInfo(relativePath)
                            break
                        case 'video':
                            meta=libDb.addVideoMetaFromFile(relativePath)
                            break
                        case 'prototype':
                            meta =libDb.addCommonMetaInfo(relativePath)
                            break
                        case 'url':
                            meta =libDb.addUrlMetaInfo(relativePath)
                            break
                        default:
                            meta=libDb.addCommonMetaInfo(relativePath)
                            break
                    }
                    if(meta.type==='folder'){
                        window.app.$store.commit('subFolder/assignSubFolderMeta',meta)
                    }else{
                        window.app.$store.commit('content/assignContentMeta',meta)
                    }
                    //
                }
            }
        })
        return newFiles
    },
    setNotNew(uuid) {
        db.get('content_meta').find({uuid: uuid}).assign({'new': false}).write()
    },
    /**
     * meta
     * @param oldPath
     * @param newPath
     */
    renameMetaFolder(oldPath,newPath){
        db.get('content_meta').forEach((meta)=>{
            if(pathTools.convertToCommonRelativeDir(meta.relative_path_full).startsWith(oldPath)){
                ///ab  /abc meta
                //console.log('brefore meta=',meta.relative_path_full)
                meta.folder=pathTools.removeLastSlash(pathTools.convertToCommonRelativeDir(meta.folder).replace(oldPath,newPath))
                meta.relative_path=pathTools.removeLastSlash(pathTools.convertToCommonRelativeDir(meta.relative_path).replace(oldPath,newPath))
                meta.relative_path_full=pathTools.removeLastSlash(pathTools.convertToCommonRelativeDir(meta.relative_path_full).replace(oldPath,newPath))
                //console.log('assigned meta=',meta.relative_path_full)
                //console.log('')
            }
        }).write()
    },
    setRecentContents(recentContents){
        db.set('recent_contents',recentContents).write()
    },
    getRecentContents(){
        this.reload()
        return db.get('recent_contents').value()
    }
}

//module.exports = configDb
export default libDb
