<template>
  <div>

    <a-modal :visible="visible" :title="title" :width="width" @ok="handleOk" okText="" cancelText=""
             @cancel="visibleChange">
      <div>
        <slot name="message">
          <p>{{ message }}</p>
        </slot>
      </div>
      <div>
        <a-input ref="input" @keyup.enter="handleOk" :defaultValue="defaultValue"></a-input>
      </div>
    </a-modal>
  </div>
</template>

<script>
export default {
  name: 'PromptModal', //todo 
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    width:
        {
          type: Number,
          default: 300
        },
    message: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    defaultValue: {
      type: String,
      default: ''
    },

  },
  model: {
    event: ['changeVisible', 'handleOk'],
  },
  watch: {
    defaultValue: {
     // immediate: true,
      handler(newValue) {
        this.$nextTick(()=>{
          this.$refs.input.setValue(newValue)
          setTimeout(()=>{
            this.$refs.input.input.select()
          },100)

        })

        //this.$refs.input.setValue(newValue)
        //this.$refs.input.setValue(this.defaultValue)

      }
    },
    // visible:{
    //   immediate:true,
    //   handler(newValue){
    //     if(newValue===true){
    //       console.log('a')
    //       console.log(this.$refs.input.setValue(this.defaultValue))
    //       //this.$refs.input.setValue(this.defaultValue)
    //     }
    //   }

  }
  ,
  data() {
    return {}
  },
  mounted() {
  },
  methods: {
    handleOk(e) {
      this.$emit('handleOk', {
        e: e, value: this.$refs.input.input.value, input: this.$refs.input//})
      })
    },
    visibleChange() {
      this.$emit('changeVisible', !this.visible)
    }
  },
  computed: {}
}
</script>

