<template>
  <div @dblclick="openDetail(content)" @click="setSelected" @mouseenter="handleMouseEnter" @mouseleave="handleMouseLeave" @mousemove="handleMouseMove" class="cover-wrapper"
       style="position: relative" :style="{width:itemSize+'px',height:itemSize+'px'}">
    <div class="type-label" :style="{display:this.progressVisible==='none'?'inline-block':'none'}"></div>
    <!--    <a-image :style="{width:itemSize+'px',height:itemSize+'px'}"-->
    <!--             :preview="false" style="background: #f1f1f1"-->
    <!--             class="sub-folder-cover"-->
    <!--             src="./img/type/pwd.svg">-->
    <!--    </a-image>-->
    <!--    <video class="content-cover sub-folder-cover" :style="{width:itemSize+'px',height:itemSize+'px'}" controls >-->
    <!--      <source :src="'file://'+content.baseInfo.path+content.baseInfo.filename" type="video/mp4">-->
    <!--      <source src="movie.ogg" type="video/ogg">-->
    <!--      <source src="movie.webm" type="video/webm">-->
    <!--      <object :data="'file://'+content.baseInfo.path+'/'+content.baseInfo.filename" :style="{width:itemSize+'px',height:itemSize+'px'}">-->
    <!--        <embed src="movie.swf" width="320" height="240">-->
    <!--      </object>-->
    <!--    </video>-->
    <!--  -->
    <!--    <span class="item-box-view-progress" v-show="item.Type == 1 && item.showPlay && item.showModal">-->
    <!--<el-row class="item-box-view-progress-ratio">-->
    <!--<el-col :span="videoplayerprogress"><div class="item-box-view-progress-ratio-arrive"></div></el-col>-->
    <!--<el-col :span="24 - videoplayerprogress"-->
    <!--&gt;<div class="item-box-view-progress-ratio-surplus"></div-->
    <!--&gt;</el-col>-->
    <!--</el-row>-->
    <!--</span>-->
    <span style="position: absolute;top: 5px;z-index: 99;margin-inline: 5px"
          :style="{width:itemSize-10+'px',display:progressVisible}">
      <a-progress :percent="progress" size="small" :showInfo="false"/>
    </span>
    <video @dblclick="openDetail(content)" controls ref="videoPlayer" class="video-js content-cover" style="position: absolute;top:5px"></video>
  </div>
  <div class="folder-name">{{ content.name }}</div>
  <div class="ext"><span >{{ content.ext.substr(content.ext.lastIndexOf('.')+1) }}</span> {{ duration }}</div>
</template>

<script>
import {mapState} from 'vuex'
import Video from 'video.js'
import 'video.js/dist/video-js.min.css'
import pathTools from '@/utils/pathTools'

window.$video = Video

export default {
  name: 'TypeVideo',
  emits:['setSelected','openDetail'],
  props: {
    content: Object
  },
  components: {},
  computed: {
    ...mapState({
      itemSize: state => state.ui.itemSize
    })
  },
  data() {
    return {
      duration: 'loading',
      progressVisible: 'none',
      player: null,
      loading: true,
      videoOptions: {
        // autoplay: "muted", //
        autoplay: false,
        controls: false,
        loop: true, //
        muted: true, //
        aspectRatio: '1:1', //
        poster: '',
        width: this.itemSize,
        height: this.itemSize,
        fullscreen: {
          options: {navigationUI: 'hide'},
        },
        sources: '',
      },
      progress: 0,
    }

  },
  created() {
    this.videoOptions.sources ='file://' + pathTools.join(this.content.baseInfo.path , this.content.baseInfo.filename)//
    this.videoOptions.poster = this.poster //poster
  },
  mounted() {
    let _this = this
    //onPlayerReady
    this.player = window.$video(this.$refs.videoPlayer, this.videoOptions, function onPlayerReady() {
      this.on('error', function () {
        //this.errorDisplay.close_() //
      })
      this.on('canplaythrough', function () {
        _this.loading = false
      })
      this.on('canplay', () => {
        let totalTime=this.duration()
        this.play()
        _this.duration = (totalTime/60).toFixed(0)+'' +(totalTime-(totalTime/60).toFixed(0)*60).toFixed(0) + ''
        setTimeout(()=>this.pause(),5000)
      })
    })
  },
  methods: {
    openDetail(content){
      this.$emit('openDetail',content)
    },
    setSelected(){
      console.log('setselecet')
      this.$emit('setSelected')
    },
    handleMouseEnter() {
      this.progressVisible = 'inline-block'
      if (this.player) {
        this.player.play()
      }
      this.$emit('videoTime', this.player.duration())
    },
    handleMouseLeave() {
      this.progressVisible = 'none'
      if (this.player) {
        this.player.pause()
      }
    },
    handleMouseMove(event) {//event
      var videoleft = this.$refs.videoPlayer.getBoundingClientRect().left//video
      var clientx = event.clientX //
      var videoWidth = this.$refs.videoPlayer.clientWidth //div
      var ratio = (clientx - videoleft) / videoWidth //
      var videoduration = this.player.duration() //
      this.player.currentTime(videoduration * ratio)
      this.progress = Number((ratio * 100).toFixed(0))
     // this.$emit('videoratio', ratio)
    },
    // videoratio(value) {
    //   console.log(value)
    //   this.videoplayerprogress = Math.round(24 * value)
    // },
  }
}
</script>

