<template>
  <PromptModal :width="400" title="" :visible="promptVisible" @handleOk="handleCreateFolder"
               @changeVisible="(visible)=>this.promptVisible=visible">
    <template #message>
       
      <blockquote class="pathBlock">
        {{ this.selectedDir }}
      </blockquote>
    </template>
  </PromptModal>
  <PromptModal message="" title=""
               :visible="promptVisibleCreateChild"
               @handleOk="handleCreateSubFolder()"
               @changeVisible="(visible)=>this.promptVisibleCreateChild=visible"
  >
    <template #message>
       
      <blockquote class="pathBlock">
        {{ this.selectedDir }}
      </blockquote>
    </template>
  </PromptModal>
  <div class="tree-root" :class="{'active-root':this.selectedKeys[0]===''}" style="font-size: 13px;margin-top: 5px;cursor:pointer;" @click="select([''])"><HomeOutlined /> </div>
  <a-empty description="" :image="simpleImage" v-if="showTree.length===0"/>
  <a-directory-tree :block-node="true" v-model:selectedKeys="selectedKeys" ref="tree" v-else class="tree"
                    :field-names="fields"
                    :tree-data="showTree"
                    @select="select"
                    v-model:expanded-keys="expandedKeys"
                    :auto-expand-parent="autoExpandParen"
  >
    <template #title="{ path, parentDir,relativePath,title,contentCount ,dirname}">
      <a-dropdown :trigger="['contextmenu']" >
        <span class="title-span" v-if="title.indexOf(searchValue)>-1">
          {{ title.substr(0, title.indexOf(searchValue)) }}<span style="color: #f50">{{ searchValue }}</span>{{ title.substr(title.indexOf(searchValue) + searchValue.length) }}
          <span v-if="Number(contentCount>0)">
            ({{contentCount}})
          </span>
        </span>
        <span class="title-span" v-else>
          {{title}} <span v-if="Number(contentCount>0)">
            ({{contentCount}})
          </span>
        </span>
        <template #overlay>
          <a-menu>
            <a-menu-item key="create" @click="createFolder(parentDir)"></a-menu-item>
            <a-menu-item key="createChild" @click="createSubFolder(relativePath)"></a-menu-item>
<!--            <a-menu-item key="move"></a-menu-item>-->
            <a-menu-item @click="openDir(relativePath)" key="locate"></a-menu-item>
            <a-menu-divider></a-menu-divider>
            <a-menu-item @click="renameFolder(relativePath,dirname)" key="rename"></a-menu-item>
            <a-menu-item @click="removeDir(path)" key="remove"></a-menu-item>
            <a-menu-divider></a-menu-divider>
            <a-menu-item @click="refresh" key="locate"></a-menu-item>
          </a-menu>
        </template>
      </a-dropdown>
    </template>
  </a-directory-tree>
</template>

<script>
// // import { defineComponent, ref, watch } from 'vue';
// import { Empty } from 'ant-design-vue';

import PromptModal from "@/components/util/PromptModal";
import ipc from "@/utils/ipc";
import Empty from "ant-design-vue/es/empty/empty";
import {HomeOutlined} from '@ant-design/icons-vue'
import _ from 'lodash'
const getParentKey = (key, tree) => {
  let parentKey;

  for (let i = 0; i < tree.length; i++) {
    const node = tree[i];

    if (node.children) {
      if (node.children.some(item => item.key === key)) {
        parentKey = node.key;
      } else if (getParentKey(key, node.children)) {
        parentKey = getParentKey(key, node.children);
      }
    }
  }

  return parentKey;
};

function filterTree(treeData,treeKeys){
  let newArr=_.filter(treeData,(item)=>{
    return _.includes(treeKeys,item.key)
  })
  return newArr.map(item=>{
    if(item.children){
      item.children=filterTree(item.children,treeKeys)
      return item
    }
  })
}
export default {
  name: 'TreeList',
  props: ['treeData', 'fields','searchValue','flatData'],
  components: {
    PromptModal,
    HomeOutlined
  },
  emits:['onSelect'],
  data() {
    return {
      promptVisible: false,
      promptVisibleCreateChild: false,
      selectedDir: '',
      simpleImage:Empty.PRESENTED_IMAGE_SIMPLE,
      selectedKeys:[],
      filterKeys:null,

      expandedKeys:[],
      autoExpandParen:true,

      subFolderHandler: window.app.$refs['subFolderHandler']
    }
  },
  watch:{
    searchValue(newValue){
      let mapped=[]
      if(newValue!==''){
        const expanded = this.flatData.map(item => {
          if (item.title && item.title.indexOf(newValue) > -1) {
            mapped.push(item.key)
            return getParentKey(item.key, this.treeData);
          }
          return null;
        }).filter((item, i, self) => item && self.indexOf(item) === i);
        this.expandedKeys = expanded;
        this.filterKeys=expanded.concat(mapped)
        this.autoExpandParent = true;
      }else{
        this.filterKeys=null
      }
    }
  },
  computed:{
    showTree(){
      let data=_.cloneDeep(this.treeData)
      if(this.filterKeys!==null){
        return filterTree(data,this.filterKeys)
      }else{
        return this.treeData
      }
    }
  },
  methods: {
    /**
     * 
     */
    refresh(){
      window.app.$store.dispatch('subFolder/updateTreeData')
    },
    /**
     * 
     * @param relativePath
     */
    openDir(relativePath){
      ipc.openDir(this.$store.state.config.storePath+relativePath)
    },
    select(selectedKeys, e){
      if(selectedKeys[0]===''){
        this.selectedKeys=[]
      }
      this.selectedKeys=selectedKeys
      this.$emit('onSelect',selectedKeys,e)
    },

    /**
     * 
     * @param parentDir
     */
    createFolder(parentDir) {
      this.selectedDir=parentDir
      this.promptVisible = true
    },
    /**
     * 
     * @param arg
     */
    handleCreateFolder(arg) { //
      ipc.createDir(this.$store.state.config.storePath + this.selectedDir, arg.value, this.$message)
      arg.input.setValue('')
      this.promptVisible = false
      //this.promptVisible=true
    },

    /**
     * 
     * @param relativePath
     * @param dirname
     */
    renameFolder(relativePath,dirname){
      this.subFolderHandler.renameFolder(relativePath,dirname)
    },


    /**
     * 
     * @param relativePath
     */
    createSubFolder(relativePath) {
      this.selectedDir='/'+relativePath
      this.promptVisible = true
    },
    /**
     * 
     * @param arg
     */
    handleCreateSubFolder(arg){//
      ipc.createDir(this.$store.state.config.storePath + this.selectedDir, arg.value, this.$message)
      arg.input.setValue('')
      this.promptVisible = false
    },


    /**
     * 
     * @param path
     */
    removeDir(path){
      this.subFolderHandler.trashItem(path)
    }
  }
}


// export default defineComponent({
//   name:'TreeList',
//   props:['treeData','fields'],
//   setup() {
//     const expandedKeys = ref([]);
//     const selectedKeys = ref([]);
//     const filedsMap=ref({
//       subFolders:'children',
//       children:'subFolders',
//       dirname:'title',
//       title:'dirname',
//       path:'key'})
//     watch(expandedKeys, () => {
//       console.log('expandedKeys', expandedKeys);
//     });
//     watch(selectedKeys, () => {
//       console.log('selectedKeys', selectedKeys);
//     });
//     return {
//       expandedKeys,
//       selectedKeys,
//       simpleImage:Empty.PRESENTED_IMAGE_SIMPLE
//     };
//   },
//
// });
</script>
<style scoped>
.pathBlock {
  padding: 5px;
  padding-left: 10px;
  background: #f1f1f1;
  color: #666;
  margin-top: 5px;
  font-size: 12px
}
.title-span{
  min-height: 24px;
  display: inline-block;
  width: 100%;
}
</style>
