<template>
  <div @dblclick="openDetail(content)" @click="setSelected" class="cover-wrapper" style="position: relative" :style="{width:itemSize+'px',height:itemSize+'px'}">
    <div class="type-label"></div>
    <PhotoCard onerror="this.src='./img/default.svg'"  @onOver="zindex=999" @onLeave="zindex=0" style="background: #f1f1f1;padding: 0" :style="{'z-index':zindex,width:itemSize+'px',height:itemSize+'px'}"  class=" sub-folder-cover content-cover" :img="cover" link=""></PhotoCard>

    <img v-if="content.meta.ext_data.favicon!==''" onerror="this.src='./img/default.svg'" :src="content.meta.ext_data.favicon" class="favicon" style="bottom: -10px;right: -10px">
  </div>
  <div class="folder-name" style="position: relative">{{ content.meta.name }}</div>
  <div class="ext"><span >{{content.meta.href }}</span></div>
</template>

<script>
import { mapState } from 'vuex'
// import {ZoomInOutlined} from '@ant-design/icons-vue';
import PhotoCard from '@/components/content/type/PhotoCard'
import contentTypeHelper from "@/utils/contentTypeHelper";
export default {
  name: 'TypeUrl',
  emits:['setSelected','openDetail'],
  components:{
    PhotoCard
  },
  props: {
    content: Object
  },
  data(){
    return {
      zindex:0,
      contentTypeHelper
    }
  },
  computed: {
    ...mapState(
      {
        itemSize: state => state.ui.itemSize
      }
    ),
    cover(){
      if(this.content.meta.cover){
        if(this.content.meta.cover.startsWith('/')){
          return 'file://'+this.$store.state.config.storePath+this.content.meta.cover
        }else{
          return this.content.meta.cover
        }
      }else{
        return './img/default.svg'
      }
    }
  },
  methods:{
    openDetail(content){
      this.$emit('openDetail',content)
    },
    setSelected(){
      this.$emit('setSelected')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

