/**
 * 
 * @type {{mutations: {updateContents(*, *): void}, state: (function(): {contents: [{extData: {domain: string, pwd: string, username: string}, name: string, href: string, type: string}]}), getters: {}}}
 */
const user= {
  namespaced:true,
  state:()=>({
    userInfo:{
      nickname:'',
      avatar:'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?undefined',
      uid:0,
      spaceUrl:'https://s.apps.vip/user',
      userToken:'',
      token:'',
      userData:{}
    },
    folders:[]
  }),
  mutations:{
    /**
     * 
     * @param state
     * @param user 
     */
    setUserInfo(state,user){
      if(user){
        state.userInfo={
          nickname:user.nickname,
          avatar:user.avatar,
          uid:user.uid,
          spaceUrl:'https://s.apps.vip/user',
          userToken:user.token,
          userData:user
        }
      }else{
        state.userInfo={
          nickname:'',
          avatar:'',
          uid:0,
          spaceUrl:'',
          userToken:'',
          userData:null
        }
      }
    }
  },
  getters:{

  },
  actions:{
  }
}

export default user
