import contentModel from "@/utils/model/contentModel";
import pathTools from "@/utils/pathTools";
import subFolderModel from '@/utils/model/subFolderModel'
import _ from 'lodash'
import libDb from '@/utils/db/libDb'
import fileHelper from '@/utils/fileHelper'
const fs = eval('require')('fs')
/**
 * 
 * @type {{mutations: {updateContents(*, *): void}, state: (function(): {contents: [{extData: {domain: string, pwd: string, username: string}, name: string, href: string, type: string}]}), getters: {}}}
 */
const content = {
  namespaced:true,
  state: () => ({
    recentContents:[],// {uuid:xxx,useTime:}
    contents: [
    ]
  }),
  mutations: {
    unshiftRecent(state,uuid){
     let foundIndex= _.findIndex(state.recentContents,{uuid:uuid})
      if(foundIndex>=0){
        state.recentContents.splice(foundIndex,1)
      }
      state.recentContents.unshift({
        uuid,
        time:Date.now()
      })
      libDb.setRecentContents(state.recentContents)
    },
    /**
     * 
     * @param state
     * @param contents
     */
    updateContents (state, contents) {
      state.contents = contents
    },
    /**
     * uuid
     * @param state
     * @param uuid
     * @param content
     */
    updateContentWithUuid(state,args){
      state.contents.forEach((ct)=>{
        if(ct.meta.uuid===args.uuid){
          let all=pathTools.getAllFromRelativeFullPath(args.meta.relative_path_full)
          ct.baseInfo.relativePath=all.relativeFolder
          ct.baseInfo.filename=all.filename
          ct.name=args.meta.name
          ct.meta=args.meta
        }
      })
    },
    assignContentMeta(state,meta){
      if(typeof meta.relative_path_full === 'undefined') return
      let foundContent=null
     state.contents.forEach((content)=>{
        if(pathTools.convertToCommonRelativePath(pathTools.join(content.baseInfo.relativePath,content.baseInfo.filename))===meta.relative_path_full){
          let newContent=contentModel.getContentFromFile(meta.relative_path_full)
          content={}
          content=newContent
          foundContent=content
        }
      })
      if(!foundContent){
         state.contents.unshift(contentModel.getContentFromFile(meta.relative_path_full))
      }
    },
    removeContent(state,content){
      for(let i=0;i<state.contents.length;i++){
        if(state.contents[i].meta.uuid===content.meta.uuid){
          state.contents.splice(i,1)
        }
      }
    },
    setRecentContents(state,recent){
      state.recentContents=recent
    }
  },
  getters: {
    contentsOrderByCreateTime(state){
      function compare(property){
        return function(a,b){
          var value1 = a.baseInfo[property];
          var value2 = b.baseInfo[property];
          return  value2-value1;
        }
      }
      return state.contents.sort(compare('createTime'))
    },
    contentsOrderByConfig(state,getters,rootState){
      if(rootState.ui.currentTab.name === 'recent'){
        return getters.contentsOrderByCreateTime
      }
      function mapType(type,meta,searchWords){
        let mapped=false
        Object.keys(type).forEach(key=>{
          let useType=type[key]
          if(useType){
            if(meta[key].toLowerCase().indexOf(searchWords)>-1)
            {
              mapped=true
              return false
            }
          }
        })
        return mapped
      }
      let result=state.contents
      if(rootState.ui.orders[0]==='create_time'){
        result= getters.contentsOrderByCreateTime
      }else if(rootState.ui.orders.length===0){
        result= state.contents
      }
      let searcher=rootState.ui.searcher //
      let searchWords=searcher.words.toLowerCase() // //todo
      if(searchWords==='')
      {
        //,
        return result
      }else{
        //
        //1.
        //2.
        let searchPath//
        let searchSubFolder=searcher.searchSubFolder
        if(searcher.searchRoot){
          //
          searchPath='/'
          searchSubFolder=true //
        }else{
          searchPath=rootState.ui.currentFolder.all.relativeFullPath
        }

        if(searchSubFolder){
          //
          let subFolderContents= contentModel.searchSubFolder(searchPath)
          let newResult=[]
         subFolderContents.map((meta)=>{
            //3. 
            if(meta.type!=='folder' && mapType(searcher.type,meta,searchWords)){
              newResult.push(contentModel.getContentFromUuid(meta.uuid))
            }
          })
          return newResult
        } else{
          //
          result= result.filter((ct)=>{
            return mapType(searcher.type,ct.meta,searchWords)
          })
        }
        return result
      }
    },

  },
  actions:{
    loadRecentContents(context){
      let recentContents=libDb.getRecentContents() || []
      context.commit('setRecentContents',recentContents)
    },
    async  changeToRecent (context) {
      await context.dispatch('loadRecentContents')
      let contents=context.state.recentContents.map((ct)=>{
        return contentModel.getContentFromUuid(ct.uuid)
      })
      context.commit('updateContents',contents)
      context.commit('ui/setOrders',[],{root:true})
      context.commit('subFolder/setCurrentSubFolders', [], {root: true})
    },
    loadFolderContents(context,path){
      //
      let files = contentModel.loadLocalStoreContents(path)
      let folderAll=pathTools.getAllFromRelativeFullPath(path)
      let config=context.rootState.ui.config //uiconfig
      let contents = []//
      let subFolders = []//
     let  filteredFiles=files
      //
      if(context.rootState.ui.filters.length>0){
        filteredFiles=files.filter(file=>{
         if(!file.stat.isFile()) return true
          let mapped=false
          //
          context.rootState.ui.filters.forEach(ft=>{
            if(ft(file)){
              mapped=true
            }
          })
          return mapped;
        })
      }
      filteredFiles.forEach(file => {
        let filename = file.filename
        let fileStorePath=pathTools.join(folderAll.storeFullPath,file.filename) //
        let fileRelativePath=pathTools.join(folderAll.relativeFolder,file.filename) //
        if (filename.startsWith('.DS_Store'))
        {
          return
        }
        const fileInfo = fs.statSync(fileStorePath)
        const all=pathTools.getAllFromRelativeFullPath(fileRelativePath)
        const  specialFileType=[
          'pxd' //pxd
        ]
        if ( (specialFileType.indexOf(all.extNoDot)>-1 || fileInfo.isFile() )&& fileHelper.ignore.indexOf(filename) ===-1) {
          let content =contentModel.getContentFromFile(fileRelativePath)
          contents.push(content)
        } else if (fileInfo.isDirectory() && config.showSubFolders) {
          //
          if(file.filename==='_assets') return
          let subFolder=subFolderModel.getSubFolderFromFolder(fileRelativePath)
          subFolders.push(subFolder)
        }
      })
      context.commit('content/updateContents', contents, {root: true})
      context.commit('subFolder/setCurrentSubFolders', subFolders, {root: true})
      let newFiles= window.libDb.scanLib(folderAll.storeFullPath)
      if(newFiles.length>0){
        window.app.$message.success({content:' '+newFiles.length+' New'})
      }
    }
  }
}

export default content
