<template>
  <div class="user-info">

    <a-row v-if="userInfo.uid">
      <a-col flex="60px">
        <a :href="getComProtocolUrl(server.userSpaceUrl)"><a-avatar :src="userInfo.avatar" :size="50"></a-avatar></a>
      </a-col>
      <a-col flex="auto">
        <div><a :href="getComProtocolUrl(server.userSpaceUrl)" target="_blank" class="nickname">{{ userInfo.nickname }}</a></div>
        <div>
          <a-progress title="99%" :percent="99" size="small"/>
        </div>
        <div><span class="logout" @click="logout()"></span></div>
      </a-col>
    </a-row>
    <a-row v-else style="text-align: center">
      <div
        style="text-align: center;background: white;padding: 10px;border-radius: 4px;box-shadow: 0 0 4px rgba(124,124,124,0.35)">
        <a-button v-if="isApp" block type="primary" @click="goAppLogin()" size="small" style="margin-bottom: 10px">
        </a-button>
        <div style="font-size: 12px;text-align: left;color: #666"><strong></strong><strong></strong>

          <a v-if="isApp"
             href="tsb://app/redirect/?package=com.thisky.helper&url=https://www.yuque.com/tswork/browser/gynywc"></a>
          <a v-else target="_blank" href="https://www.yuque.com/tswork/browser/gynywc"></a>

        </div>
      </div>

    </a-row>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import tools from '@/utils/tools'

export default {
  name: 'UserInfo',
  props: {
    msg: String
  },
  data () {
    return {
      isApp: window.isApp
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.user.userInfo,
      server: state=>state.config.server
    })
  },
  methods: {
    getComProtocolUrl:tools.getComProtocolUrl,
    logout () {
      this.$store.commit('user/setUserInfo', { uid: 0 })
    },
    goAppLogin () {
      //
      window.ipc.send('addTab', { url: this.server.loginUrl })
      let timer = setInterval(() => {
        window.ipc.send('getUserInfo')
        if (this.userInfo.uid !== 0) {
          clearInterval(timer)
        }
      }, 1000)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.user-info {
  margin-bottom: 10px;

}

.nickname {
  color: #333;
  cursor: pointer;
}

.logout {
  font-size: 12px;
  color: #999;
  cursor: pointer;
}
</style>
