<template>
  <router-view></router-view>

  <SubFolderHandler ref="subFolderHandler"/>
</template>
<script>
import { mapState } from 'vuex'
import tools from '@/utils/tools'

import SubFolderHandler from '@/components/content/SubFolderHandler'
tools.getWindowArgs(window)
import configDb from '@/utils/db/configDb'
import libDb from '@/utils/db/libDb'
configDb.init(window.globalArgs['user-data-dir'])//configDBstorePath
window.configDb=configDb//
const defaultStorePath =  configDb.getStorePath()+'/' //window.globalArgs['user-data-dir'] + '/.lab'+'/'
window.$defaultStrorePath=defaultStorePath
libDb.init(defaultStorePath) //libDB
window.libDb=libDb //
export default {
  name: 'App',
  components: {
    SubFolderHandler,
    // HelloWorld,
  },
  data(){
    return{
      isApp:window.isApp
    }
  },
  computed:{
    ...mapState(
      {
        userInfo:state=>state.user.userInfo,
        storePath:state=>state.config.storePath
      }
      )
  },
  mounted () {
    //StorePath//todo 
    this.$store.commit('config/setStorePath',defaultStorePath)
  },
  methods:{

  }
}
</script>

