import configDb from '@/utils/db/configDb'
import fileHelper from '@/utils/fileHelper'

const low = eval('require')('lowdb')
const FileSync = eval('require')('lowdb/adapters/FileSync')
//const fs = eval('require')('fs')
const libPath = configDb.getStorePath()
const indexSavePath = libPath + '/_index.json'

const indexModel = {
  db: null,
  index:[],
  initialize () {
    const adapter = new FileSync(indexSavePath)
    const db = low(adapter)
    indexModel.db = db
    if (!db.get('lastUpdateTime').value()) {
      indexModel.buildIndex()
    }
    return db
  },
  /**
   * 
   */
  buildIndex () {
      indexModel.updateIndexByFolder(libPath)
  },
  /**
   * 
   * @returns {{index: [], lastUpdateTime}} {index:lastUpdateTime:}
   */
  getAll () {
      return {
        index:indexModel.index,
        lastUpdateTime:indexModel.db.get('lastUpdateTime').value()
      }
  },
  /**
   * ,
   * @param dir
   */
  updateIndex(dir=libPath){
    indexModel.updateIndexByFolder(dir)
  },
  /**
   * 
   */
  updateIndexByFolder (dir=libPath) {
    const index=fileHelper.getFileTree(dir,{relative:true})
    indexModel.db.set('index',index).write()
    indexModel.db.set('lastUpdateTime',Date.now()).write()
    indexModel.index=index
  },

}

export default indexModel
