<template>
  <a-modal v-model:visible="askModelVisible" :closable="false" :centered="true">
    <template #footer>
<!--      <a-checkbox></a-checkbox>-->
      <a-button  @click="this.askModelVisible=false;this.copyContent(this.askingContent)"></a-button>
      <a-button @click="this.askModelVisible=false;this.moveContent(this.askingContent)"></a-button>
      <a-button @click="stopMove"></a-button>
    </template>
    <p>"<strong>{{ askingContent.filename }}</strong>"</p>
  </a-modal>
  <PromptModal :width="400" title="" :defaultValue="renameFolderName" :visible="promptRenameVisible" @handleOk="handleRenameFolder"
               @changeVisible="(visible)=>this.promptRenameVisible=visible">
    <template #message>
      
      <blockquote class="pathBlock">
        {{ this.selectedDir }}
      </blockquote>
    </template>
  </PromptModal>
</template>
<script>
import PromptModal from "@/components/util/PromptModal";
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
import { Modal } from 'ant-design-vue'
import pathTools from '@/utils/pathTools'
import contentModel from '@/utils/model/contentModel'
import statusReturn from '@/utils/statusReturn'
import subFolderModel from '@/utils/model/subFolderModel'
const fs = eval('require')('fs')
export default {
  name: 'SubFolderHandler',
  props: {
    msg: String
  },
  components: {
    PromptModal
  },
  data () {
    return {
      askModelVisible: false,
      waitingMoveList: [],//
      movedContentCount: 0,//
      failureContentCount: 0,//
      askingContent: {},//
      targetFolder: null,
      onStop:()=>{}, //



      promptRenameVisible:false,
      renameFolderName:'',
    }
  },
  computed: {
    ...mapState({
      focusItem: state => state.ui.focusedItem,
    })
  },
  methods: {
    /**
     * 
     * @param arg
     */
    handleRenameFolder(arg) { //
      ipc.renameFolder(this.selectedDir,arg.input.input.value)
      arg.input.setValue('')
      this.promptRenameVisible = false
    },
    /**
     * 
     * @param relativePath
     * @param dirname
     */
    renameFolder(relativePath,dirname){
      this.selectedDir=relativePath.startsWith('/')?relativePath:'/'+relativePath
      this.renameFolderName=dirname
      this.promptRenameVisible=true
    },


    /**
     * 
     */
    moveNextContent () {
      let folder = this.targetFolder
      //
      if (this.waitingMoveList.length <= 0) {
        this.$message.success(` ${this.movedContentCount} `)
        folder.hover = false
        return
      }
      let nextContent = this.waitingMoveList.shift()
      let all = pathTools.getAllFromRelativeFullPath(nextContent.meta.relative_path_full)
      let allTarget=pathTools.getAllFromRelativeFullPath(folder.meta.relative_path_full)
      if (fs.existsSync(allTarget.storeFullPath+'/'+all.filename)) {
        //
        this.askingContent = nextContent
        this.askingContent.filename = all.filename
        this.askModelVisible = true
        return
      } else {
        this.moveContent(nextContent)
      }

    },
    /**
     * 
     * @param list list
     * @param folder 
     * @param onStop ui
     */
    startMoveContents (list,folder,onStop) {
      if(list.length===0){
        return
      }
      if(onStop!==undefined){
        this.onStop=onStop
      }
      this.waitingMoveList = list
      this.targetFolder=folder
      this.movedContentCount = 0
      this.failureContentCount = 0
      this.moveNextContent(folder)
    },
    stopMove () {
      this.askModelVisible = false
      this.onStop()
    },
    /**
     * 
     * @param content
     * @param moveNext
     */
    copyContent(content,moveNext=true){
      let allSource=pathTools.getAllFromRelativeFullPath(content.meta.relative_path_full)
      let targetPath=this.targetFolder.meta.relative_path_full+'/'+allSource.name+allSource.ext
      let testRelativePath=targetPath
      let testPath=pathTools.getStorePathFromRelativePath(testRelativePath)
      let testNo=1
      while(fs.existsSync(testPath)){
        testRelativePath=this.targetFolder.meta.relative_path_full+'/'+allSource.name+' ' + (testNo>1?String(testNo):'')+allSource.ext
        testPath=pathTools.getStorePathFromRelativePath(testRelativePath)
        testNo++
      }
      let result=contentModel.move(content.meta.uuid, testRelativePath, false)
      if (result.status === statusReturn.successCode) {
        this.movedContentCount=this.movedContentCount+1
        this.$store.commit('content/removeContent',content)
        this.$store.commit('ui/clearFocusItem')
      } else {
        this.failureContentCount=this.failureContentCount+1
      }
      if(moveNext)
        this.moveNextContent()
      //1
    },
    /**
     *
     * @param content 
     * @param moveNext 
     */
    moveContent (content,moveNext=true) {
      let allSource=pathTools.getAllFromRelativeFullPath(content.meta.relative_path_full)
      let result=contentModel.move(content.meta.uuid, this.targetFolder.meta.relative_path_full+'/'+allSource.filename, true)
      if (result.status === statusReturn.successCode) {
        this.movedContentCount=this.movedContentCount+1
        this.$store.commit('content/removeContent',content)
        this.$store.commit('ui/clearFocusItem')
      } else {
        this.failureContentCount=this.failureContentCount+1
      }
      if(moveNext)
        this.moveNextContent()
    },
    open: ipc.openExternal,
    trashItem (path) {
      Modal.confirm({
        title: '',
        content: '',
        okText: '',
        cancelText: '',
        onOk:async () => {
          let result=await subFolderModel.remove(path)
          if(result.isSuccess){
            this.$message.success(''+result.data.removedCount+'')
              window.app.$store.dispatch('subFolder/updateTreeData')
              window.app.$store.dispatch('ui/reload')
          }else{
            this.$message.error(''+result.info)
          }
        }
      })
    },
  }
}
</script>
