<template>
  <div class="content-info">
    <div style="margin-bottom: 20px;height: auto;position: relative">
      <a-image  :style="{objectFit:'cover',padding:20+'px'}" :width="200" :height="190"
               :preview="false"
               :src="'./img/folder.svg'">
      </a-image>
    </div>
    <div style="margin-bottom: 10px">
      <a-textarea @blur="rename()" v-model:value="userInput.name" :autSize="{ minRows: 1, maxRows: 5 }"  style="margin-bottom: 10px" placeholder=""></a-textarea>
<!--      <a-select-->
<!--        :options="options"-->
<!--        mode="tags"-->
<!--        placeholder=""-->
<!--        style="width: 100%"-->
<!--      ></a-select>-->
      <a-textarea @blur="editRemark()" v-model:value="userInput.remark" placeholder=""></a-textarea>
      <a-rate @change="setRate" v-model:value="userInput.rate" style="margin-bottom: 10px" :value="4" allow-half></a-rate>
<!--      <a-select-->
<!--        :options="options"-->
<!--        mode="tags"-->
<!--        placeholder=""-->
<!--        style="width: 100%"-->
<!--      ></a-select>-->
    </div>
    <div style="margin-bottom: 10px">
      <h4></h4>
      <div>
        {{ this.dateStr(focusedItem.baseInfo.createTime) }}
        <br>
        {{ this.dateStr(focusedItem.baseInfo.addTime) }}
        <br>
        {{ this.dateStr(focusedItem.baseInfo.modifyTime) }}
        <br>
        <br><span title="" @click="showItem(focusedItem)" style="color: #999;cursor:pointer;">{{ focusedItem.baseInfo.relativePath }}{{focusedItem.baseInfo.filename}}</span>
        <br/>
        {{focusedItem.baseInfo.sizeText}}
      </div>
    </div>
    <div>
      <a-button @click="exportContent(focusedItem)" block></a-button>
    </div>
  </div>

</template>

<script>
import { ref, defineComponent } from 'vue'
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
import pathTools from '@/utils/pathTools'
import fileModel from '@/utils/model/fileModel'
import subFolderModel from '@/utils/model/subFolderModel'
import statusReturn from '@/utils/statusReturn'
import pathTool from '@/utils/pathTools'

export default defineComponent({
  name: 'FocusItemFolder',
  props: {},
  components:{
  },
  data () {
    return {
      ipc,
      value3: ref(['a1', 'b2']),
      options: [
        { value: '' },
        { value: '' },
        { value: '' },
        { value: '' },
      ],
      userInput:{
        name:'',//
        remark:'',//
        rate:0,//
      }
    }
  },
  mounted () {
    this.userInput.name=this.focusedItem.meta.name
    this.userInput.remark=this.focusedItem.meta.remark || ''
    this.userInput.rate=this.focusedItem.meta.rate || 0
  },
  methods: {
    showItem(item){
      ipc.showItemInFolder(pathTool.join(item.baseInfo.path,item.baseInfo.filename))
    },
    /**
     * 
     */
    rename(){
      if(this.userInput.name===this.focusedItem.meta.name){
        return
      }
      let result= subFolderModel.rename(this.focusedItem.meta.uuid,this.userInput.name)
      if(result.status===statusReturn.successCode){
        this.$message.success('')
        this.$store.commit('subFolder/updateSubFolderWithUuid',
            {uuid:this.focusedItem.meta.uuid,
              meta:result.data})
        this.$store.dispatch('subFolder/updateTreeData')
      }else{
        this.userInput.name=this.focusedItem.name
        this.$message.error(''+result.info)
      }
    },
    editRemark(){
      if(this.userInput.remark===this.focusedItem.meta.remark){
        return
      }
      let result= subFolderModel.update(this.focusedItem.meta.uuid, { remark:this.userInput.remark })
      if(result.status===statusReturn.successCode){
        //this.$message.success('')
        this.$store.commit('subFolder/updateSubFolderWithUuid',
            {uuid:this.focusedItem.meta.uuid,
              meta:result.data})
      }else{
        this.userInput.remark=this.focusedItem.meta.remark
        this.$message.error(''+result.info)
      }
    },
    setRate(){
      let result= subFolderModel.update(this.focusedItem.meta.uuid, { rate:this.userInput.rate })
      this.$store.commit('subFolder/updateSubFolderWithUuid',
          {uuid:this.focusedItem.meta.uuid,
            meta:result.data})
    },
    exportContent(content){
      let path=pathTools.getStorePathFromRelativePath(content.meta.relative_path_full)
      let parentPath=pathTools.getStorePathFromRelativePath(content.meta.relative_path)
      fileModel.saveAs(path,parentPath)
    },
    dateStr (date) {
      //js 
      var time = new Date().getTime()
      date=parseInt(date.toFixed(0))/1000
      // js php 
      time = parseInt((time - date * 1000) / 1000)
      //
      var s
      if (time < 60 * 10) {//
        return ''
      } else if ((time < 60 * 60) && (time >= 60 * 10)) {
        //1
        s = Math.floor(time / 60)
        return s + ''
      } else if ((time < 60 * 60 * 24) && (time >= 60 * 60)) {
        //124
        s = Math.floor(time / 60 / 60)
        return s + ''
      } else if ((time < 60 * 60 * 24 * 3) && (time >= 60 * 60 * 24)) {
        //13
        s = Math.floor(time / 60 / 60 / 24)
        return s + ''
      } else {
        //3
        var date2 = new Date(parseInt(date) * 1000)
        return date2.getFullYear() + '/' + (date2.getMonth() + 1) + '/' + date2.getDate()
      }
    }
  },
  watch:{
    'focusedItem.meta.name':{
      handler(newValue,oldValue){
        this.userInput.name=newValue
        oldValue
      }
    },
    'focusedItem.meta.remark':{
      handler(newValue,oldValue){
        this.userInput.remark=newValue
        oldValue
      }
    },
    'focusedItem.meta.rate':{
      handler(newValue,oldValue){
        this.userInput.rate=newValue || 0
        oldValue
      }
    }
  },
  computed: {
    ...mapState(
      {
        focusedItem: state => state.ui.focusedItem.data
      }),
    cover(){
      if(this.focusedItem.cover){
        if(this.focusedItem.cover.startsWith('/')){
          return 'file://'+this.$store.state.config.storePath+this.focusedItem.cover
        }else{
          return this.focusedItem.cover
        }
      }else{
        return ''
      }

    }
  },
  setup () {
    return {
      random: ref(),
    }
  }
})
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="text/css" scoped>
.content-info {
  padding: 15px;
  background: #f1f1f1;
}

input {
  margin-bottom: 10px;
}

.ant-select {
  margin-bottom: 10px;
}
</style>
