import ContentListPage from "@/pages/main/ContentListPage";
import ImageDetailPage from "@/pages/main/ImageDetailPage";
import PasswordListPage from "@/pages/main/PasswordListPage";
import PopSaveToFolderPage from "@/pages/PopSaveToFolderPage";
import MainPage from "@/pages/MainPage";
import VideoDetailPage from '@/pages/main/VideoDetailPage'

const routes = [
    {
      path:'/',
      redirect:'/main'
    },
    {
        path: '/main',
        component: MainPage,
        redirect: '/main/content',
        children:[
            {
                path: 'content',
                component: ContentListPage,

            },
            {
                name: 'img',
                path: 'img/:path',
                component: ImageDetailPage
            },
            {
                name: 'video',
                path: 'video/:path',
                component: VideoDetailPage
            },
            {
                name: 'pwds',
                path: 'pwds',
                component: PasswordListPage
            },
        ]
    },
    {
        name: 'popSaveToFolder',
        path: '/popSaveToFolder',
        component: PopSaveToFolderPage
    }
]
export default routes
