import contentTypeHelper from '@/utils/contentTypeHelper'
import tools from '@/utils/tools'
import pathTools from '@/utils/pathTools'
import libDb from '@/utils/db/libDb'
import statusReturn from '@/utils/statusReturn'
const fs = eval('require')('fs')
const sizeOf = eval('require')('image-size')
const contentModel = {
  addLink (folder, title, url, cover, favicon, remark,rate) {
    return window.libDb.addUrl(folder, title, url, cover, favicon, remark,rate)
  },
  getMetaFromFile (relativePath) {
    relativePath = pathTools.convertToCommonRelativePath(relativePath)
    return window.libDb.getMeta(relativePath)
  },
  /**
   * content
   * @param folder
   */
  searchSubFolder(folder){
    let folderAll= pathTools.getAllFromRelativeFullPath(folder)
    return libDb.getFolderMetas(folderAll.relativeFullPath)

  },
  /**
   * Content
   * @returns {{ext: (*|string|string|string), cover: string, baseInfo: {path, filename, modifyTime: T | T, addTime: T | T, size, createTime: T | T, sizeText: string, relativePath}, extData: {}, meta: (*|void), name, href: string, extCanOpenInside: boolean, type: (string|*)}}
   * @param relativeFullPath
   */
  getContentFromFile (relativeFullPath) {
    let all=pathTools.getAllFromRelativeFullPath(relativeFullPath)
    let fullPath = all.storeFullPath// pathTools.getFullPathFromRelativeFullPath(relativeFullPath)
    let filename = all.filename

    let path = all.storeFolder
    let relativePath = all.relativeFolder
    let fileInfo
    if(!fs.existsSync(fullPath)){
      return null
    }else{
      fileInfo= fs.statSync(fullPath)
    }

    const fileExt = filename.substr(filename.lastIndexOf('.'))

    const type = contentTypeHelper.getContentTypeNameFromExt(fileExt)
    let content = {
      extCanOpenInside: contentTypeHelper.extCanOpenInside(fileExt),
      name: filename.substr(0, filename.lastIndexOf('.')), //
      type: type,
      ext: fileExt,
      extData: {
        // width: '900',
        // height: '420'
      },
      baseInfo: {
        path: path,
        relativePath: relativePath,
        filename: filename,
        createTime: fileInfo.birthtimeMs,
        addTime: fileInfo.ctimeMs,
        modifyTime: fileInfo.mtimeMs,
        size: fileInfo.size,
        sizeText: tools.convertToText(fileInfo.size)
      },
      href: '',
    }
    let meta = contentModel.getMetaFromFile(relativeFullPath)
    content = contentModel.mergeContentAndMeta(content, meta)
    return content
  },
  getContentFromUuid (uuid) {
    libDb.reload()
    let meta = libDb.getMetaFromUuid(uuid)
    let content = contentModel.getContentFromFile(meta.relative_path_full)
    if(content===null){
      libDb.removeMeta(meta.relative_path_full)
      return null
    }
    return content
  },
  /**
   * metacontent
   * @param content
   * @param meta
   */
  mergeContentAndMeta (content, meta) {
    if (content.type === 'img') {
      let dimensions = sizeOf(pathTools.getStorePathFromRelativePath(content.baseInfo.relativePath + content.baseInfo.filename))
      content.cover = pathTools.convertToCommonRelativePath(content.baseInfo.relativePath + content.baseInfo.filename)
      content.extData.width = dimensions.width
      content.extData.height = dimensions.height
    }else if(content.type==='url'){
      if(meta.cover)
        content.cover = pathTools.convertToCommonRelativePath(meta.cover)
    }
    content.extData = Object.assign(content.extData, content.ext_data)
    content.meta = meta
    return content
  },
  removeContent (path, isFullPath = true) {
    let fullPath
    if(isFullPath){
      fullPath=path
    }else{
      fullPath=pathTools.getFullPathFromRelativeFullPath(path)
    }
    if(fs.existsSync(fullPath)){
      fs.unlinkSync(fullPath)
    }
    let relativePath = path
    if (isFullPath) {
      relativePath = pathTools.getRelativeFullPathFromFullPath(path)
    }

    libDb.removeMeta(relativePath)
  },
  rename (uuid, inputName) {
    try {
      var contentMeta = window.libDb.getMetaFromUuid(uuid)
      let oldPath=pathTools.getFullPathFromRelativeFullPath(contentMeta.relative_path_full)
      if (!contentMeta) {
        return statusReturn.failure('')
      }
      let newName = pathTools.filterFilename(inputName).filename
      let newFilename = newName + '.' + contentTypeHelper.getExtNoDotFromFilename(pathTools.getFilenameFromRelativeFullPath(contentMeta.relative_path_full))
      let newPath =pathTools.replaceSlash(pathTools.getStorePathFromRelativePath(contentMeta.relative_path)+'/'+ newFilename)
      if(!fs.existsSync(oldPath)){
        return statusReturn.failure('')
      }
      if (fs.existsSync(newPath)) {
        return statusReturn.failure('')
      } else {
        fs.renameSync(oldPath, newPath)
        if (contentMeta.base_info) {
          contentMeta.base_info.update_time = Date.now()
        } else {
          contentMeta.base_info = {
            update_time: Date.now()
          }
        }
        contentMeta.name = newName
        contentMeta.relative_path_full = pathTools.join(contentMeta.relative_path , newFilename)
        libDb.updateMeta(uuid, {
          name: contentMeta.name,
          relative_path_full: contentMeta.relative_path_full,
          base_info: contentMeta.base_info
        })
        return statusReturn.success('', contentMeta)
      }
    } catch (e) {
      console.warn(e)
      return statusReturn.failure('')
    }
  },
  /**
   * uidmeta
   * @param uuid
   * @param saveData
   * @returns {{data: {}, isFailure: boolean, status: *, info: *, isSuccess: boolean}}
   */
  update (uuid, saveData) {
    try {
      let contentMeta = libDb.getMetaFromUuid(uuid)
      contentMeta = Object.assign(contentMeta, saveData)
      libDb.updateMeta(uuid, contentMeta)
      return statusReturn.success('', contentMeta)
    } catch (e) {
      console.warn(e)
      return statusReturn.failure('')
    }
  },
  /**
   * uuid
   * @param uuid
   * @param targetPath 
   * @param replace 
   * @returns {{data: {}, isFailure: boolean, status: *, info: *, isSuccess: boolean}}
   */
  move(uuid,targetPath,replace=false){
    let exists=fs.existsSync(pathTools.getStorePathFromRelativePath(targetPath))
    if(exists && !replace){
      //
      return statusReturn.failure('')
    }
    let content=contentModel.getContentFromUuid(uuid)
    let allSource=pathTools.getAllFromRelativeFullPath(content.meta.relative_path_full)
    let allTarget=pathTools.getAllFromRelativeFullPath(targetPath)
    try{
      if(exists){
        fs.unlinkSync(allTarget.storeFullPath) //
      }
      fs.renameSync(allSource.storeFullPath,allTarget.storeFullPath)
    }catch (e) {
      return statusReturn.failure('')
    }
    if(exists){
      contentModel.removeContent(allTarget.relativeFullPath) //meta
    }
    contentModel.update(uuid,{  //meta
      name:allTarget.name,
      folder:allTarget.relativeFolder,
      relative_path:allTarget.relativeFolder,
      relative_path_full:allTarget.relativeFullPath
    })
    return statusReturn.success('',uuid)
  },
  //
  // moveList(list,target){
  //
  // }
  loadLocalStoreContents (folder) {
    let dir=pathTools.getStorePathFromRelativePath(folder)
    let files = fs.readdirSync(dir)
    let filesInfo=[]
    files.forEach(file=>{
      filesInfo.push({
        path:dir,
        filename:file,
        stat:fs.statSync(pathTools.join(dir,file)),
      })
    })
    return filesInfo
  }
}

export default contentModel
