const tools = {
  /**
   * 
   * @param pkg 
   * @param url url
   * @returns {string}
   */
  getAppProtocolUrl (pkg, url) {
    return `tsb://app/redirect/?package=${pkg}&url=${url}`
  },
  /**
   * 
   * @param url url
   * @returns {string}
   */
  getComProtocolUrl (url) {
    return tools.getAppProtocolUrl('com.thisky.com', url)
  },
  /**
   * 
   * @param window window
   * @returns {*}
   */
  getWindowArgs: (window) => {
    window.globalArgs = {}
    process.argv.forEach(function (arg) {
      if (arg.startsWith('--')) {
        var key = arg.split('=')[0].replace('--', '')
        var value = arg.split('=')[1]
        // if(key==='saApp'){
        //   console.log(decodeURI(value))
        //   value=JSON.parse(decodeURI(value))
        // }
        window.globalArgs[key] = value
      }
    })
    if (navigator.platform === 'MacIntel') {
      window.platformType = 'mac'
    } else if (navigator.platform === 'Win32') {
      window.platformType = 'windows'
    } else {
      window.platformType = 'linux'
    }
    return window
  }
  ,
  /**
   * www.www.baidu.com/search => baidu.com/search
   * @param hostname 
   * @returns {*}
   */
  getDomain: function (hostname) {
    // www
    let domain = hostname
    if (domain.startsWith('www.')) {
      domain = domain.slice(4)
    }
    return domain
  },//
  getRootDomain: function (hostname) {
    // www
    let domain = tools.getDomainFromUrl(hostname)
    let domainArray = domain.split('.')
    return domainArray[domainArray.length - 2] + '.' + domainArray[domainArray.length - 1]
  },
  /**
   * url
   * @param url
   * @returns {*}
   */
  getDomainFromUrl (url) {
    let domain = url.split('/') ///
    if (domain[2]) {
      domain = domain[2]
    } else {
      domain = '' //url
    }
    domain = tools.getDomain(domain)
    return domain
  },
  // innerHTML
  /**
   * htmlspanHtml
   * @param str
   * @returns {string}
   */
  htmlEncode (str) {
    var ele = document.createElement('span')
    ele.appendChild(document.createTextNode(str))
    return ele.innerHTML
  },

  /**
   * htmltext
   * @param str
   * @returns {string}
   */
  htmlDecode (str) {
    var ele = document.createElement('span')
    ele.innerHTML = str
    return ele.textContent
  },

  /**
   * KB
   * @param size BNumber
   * @returns {string} *MB  *GB  *KB
   */
  convertToText (size) {
    let mb = (size / 1024 / 1024).toFixed(2)
    if (mb < 1) {
      return (size / 1024).toFixed(1) + 'KB'
    } else if (mb > 1024) {
      return (size / 1024 / 1024 / 1024).toFixed(2) + 'GB'
    } else {
      return mb + 'MB'
    }
  }
}

export default tools
