<template>
  <div class="side-bar " style="overflow-x: hidden">
    <UserInfo v-if="false"/>
    <a-menu class="nav"
            v-model:selectedKeys="tab"
            style="background: none;"
            mode="inline"
            @click="handleNavClick"
    >
      <a-menu-item key="all" @titleClick="titleClick">
        <template #icon>
          <AppstoreOutlined/>
        </template>
        <span></span>
      </a-menu-item>
      <a-menu-item key="collection" @titleClick="titleClick">
        <template #icon>
          <InboxOutlined/>
        </template>
        <span></span>
      </a-menu-item>
      <a-menu-item key="recent">
        <template #icon>
          <ClockCircleOutlined/>
        </template>
        <span></span>
      </a-menu-item>
    </a-menu>

    <a-collapse class="tab-wrapper" v-model:activeKey="collapseContentType" :bordered="false" :ghost="true">
      <a-collapse-panel key="contentType" header="">
        <div>
          <a-row class="content-types" type="flex">
            <a-col v-for="content in contentTypes" :key="content" :span="12">
              <div class="type" :class="{'active':content.tab===tab[0]}" type="link"
                   @click="setTab(content.tab,content.name)">
                <FolderOutlined/>
                {{ content.name }}
              </div>
            </a-col>
          </a-row>
        </div>
      </a-collapse-panel>
    </a-collapse>
    <a-popconfirm
        ok-text=""
        cancel-text=""
        @confirm="createDir"
    >
      <template #icon>
        <div style="text-align: left;margin-bottom: 10px"></div>
      </template>
      <template #title>
        <a-input ref="dirInput" @keyup.enter="createDir" style="margin-left: -10px"></a-input>
      </template>
      <div @click.prevent.stop=""
           style="position: absolute;z-index: 99;width: 20px;height: 20px;right: 0;cursor: pointer">
        <PlusOutlined/>
      </div>
    </a-popconfirm>

    <a-collapse class="tab-wrapper" v-model:activeKey="collapseFolder" :bordered="false" :ghost="true">
      <a-collapse-panel key="folder" header="">
        <div>
          <TreeList @onSelect="setSelectedFolder" :searchValue="searchFolderValue" :flatData="userFlatFolders" :treeData="userFolders" :fields="userFolderFields"></TreeList>
        </div>

      </a-collapse-panel>
    </a-collapse>
    <!--    <a-collapse class="tab-wrapper" v-model:activeKey="collapseGroupFolder" :bordered="false" :ghost="true">-->
    <!--      <a-collapse-panel key="groupFolder" header="">-->
    <!--        <div v-for="group in groups" v-bind:key="group">-->
    <!--          <div style="margin-top: 10px">-->
    <!--            <a-avatar shape="square" :size="18" :src="group.icon"></a-avatar>-->
    <!--            {{ group.name }}-->
    <!--            <span style="float:right;"> <PlusOutlined style="color:#999;font-size: 12px"/></span>-->

    <!--          </div>-->
    <!--          <TreeList :treeData="group.folders"></TreeList>-->
    <!--        </div>-->
    <!--      </a-collapse-panel>-->
    <!--    </a-collapse>-->
  </div>
  <div style="background: rgb(241, 241, 241);position: fixed;bottom: 0;left: 0;z-index: 999; " :style="{width:dividerPos.left-15 + 'px'}"><a-input-search size="small" block v-model:value="searchFolderValue" style="width: 100%;margin: 5px" placeholder="" /></div>
</template>

<script>
import {
  AppstoreOutlined,
  InboxOutlined,
  ClockCircleOutlined,
  FolderOutlined,
  PlusOutlined
} from '@ant-design/icons-vue'
import TreeList from './TreeList.vue'
import { mapState } from 'vuex'
import UserInfo from '@/components/UserInfo'
import ipc from '@/utils/ipc'
import contentTypeHelper from '@/utils/contentTypeHelper'
// import subFolderModel from '@/utils/model/subFolderModel'

export default {
  name: 'SideBar',
  props: {
    msg: String
  },
  components: {
    UserInfo,
    AppstoreOutlined,
    InboxOutlined,
    ClockCircleOutlined,
    FolderOutlined,
    PlusOutlined,
    TreeList
  },
  data () {
    return {
      searchFolderValue:'',//
      mkdirVisible: false,//
      //
      userFolderFields: {
        subFolders: 'children',
        children: 'subFolders',
        dirname: 'title',
        title: 'dirname',
        path: 'key'
      },
      visiblePopInputDirName: false,//
      collapseContentType: ['contentType'],
      collapseFolder: ['folder'],
      collapseGroupFolder: ['groupFolder'],
      contentTypes: [
        {
          name: '',
          tab: 'bookmark'
        },
        {
          name: '',
          tab: 'pwds'
        },
        // {
        //   name: '',
        //   tab: 'app'
        // },
        // {
        //   name: '',
        //   tab: 'download'
        // },
        {
          name: '',
          tab: 'img'
        },
        {
          name: '',
          tab: 'video'
        },
        {
          name: '',
          tab: 'doc'
        },
        {
          name:'',
          tab:'audio'
        }
        // {
        //   name: '',
        //   tab: 'text'
        // }
      ]
    }
  },
  computed: {
    tab () {
      return [this.$store.state.ui.currentTab.name]
    },
    ...mapState(
        {
          groups: state => state.group.groups,
          userFlatFolders:state=>state.subFolder.myAllFoldersFlat,
          userFolders: state => state.subFolder.myAllFolders,
          dividerPos:state=>state.ui.dividerPos
        }
    )
  },
  methods: {
    /**
     * 
     * @param selectedKeys
     * @param e
     */
    setSelectedFolder (selectedKeys) {
      this.$router.push('/main/')
      this.$store.dispatch('ui/changeFolder', { path: '/' + selectedKeys[0] + '/' })
    },
    /**
     *
     */
    createDir (event) {
      event.preventDefault()
      let dirInput = this.$refs.dirInput.input.value //
      if(dirInput.trim()===''){
        window.app.$message.error({content:
          ''})
        return
      }
      ipc.createDir(this.$store.state.config.storePath, dirInput, this.$message)
      this.$refs.dirInput.setValue('')//
    },

    handleNavClick (e) {
      const tabs = {
        'all': '',
        'collection': '',
        'recent': ''
      }
      this.$router.push('/main/')
      this.setTab(e.key, tabs[e.key])
    },
    titleClick (e) {
      this.setTab(e.key)
      this.$router.push('/main/')
    },
    getFilters (tabName) {
      let bookmark = function (file) {
        return file.filename.endsWith('.url')
      }
      let fileType = function (file, type = 'img') {
        return contentTypeHelper.getContentTypeFromFilename(file.filename).name === type
      }
      let imgType = (file) => {
        return fileType(file, 'img')
      }
      let videoType = (file) => {
        return fileType(file, 'video')
      }
      let docType = (file) => {
        return fileType(file, 'doc')
      }
      let audioType=(file)=>{
        return fileType(file,'audio')
      }

      switch (tabName) {
          /*
            {
            name: '',
            tab: 'bookmark'
          },
          {
            name: '',
            tab: 'app'
          },
          {
            name: '',
            tab: 'pwds'
          },
          {
            name: '',
            tab: 'pic'
          },
          {
            name: '',
            tab: 'text'
          },
          {
            name: '',
            tab: 'video'
          },
          {
            name: '',
            tab: 'file'
          }, {
            name: '',
            tab: 'download'
          }
           */
        case 'bookmark':
          return bookmark
        case 'img':
          return imgType
        case 'video':
          return videoType
        case 'doc':
          return docType
        case 'audio':
          return audioType
        default:
          return (file)=>{
            return file
          }
      }
    },
    setTab (tabName, alias) {
      switch (tabName) {
        case 'pwds':
          this.$router.replace('pwds')
          this.$store.commit('ui/setTab', {
            name: 'pwds',
            alias: ''
          })
          break
        case 'collection':
          this.$store.commit('ui/setConfig',{showSubFolders: false})
          this.$router.replace('content')
          this.$store.dispatch('ui/changeTab', {
            name: tabName,
            alias: alias
          })
          break
        default:
          this.$store.commit('ui/setConfig',{showSubFolders: true})
          this.$store.commit('ui/setFilter', [this.getFilters(tabName)])
          this.$router.replace('content')
          this.$store.dispatch('ui/changeTab', {
            name: tabName,
            alias: alias
          })

      }
    }
  }
}
</script>
