<template>
  <div v-if="this.selectedItems.length>1">
    <SelectedItemContent />
  </div>
  <div v-else-if="this.focusedItem.type">
    <focus-item-content v-if="this.focusedItem.type==='content'"></focus-item-content>
    <focus-item-folder v-else></focus-item-folder>
  </div>
  <div v-else style="padding-top: 100px">
    <a-empty description=""/>
  </div>
</template>

<script>
import {  defineComponent } from 'vue'
import { mapState } from 'vuex'
import FocusItemContent from '@/components/FocusItemContent'
import FocusItemFolder from '@/components/FocusItemFolder'
import SelectedItemContent from '@/components/SelectedItemContent'
export default defineComponent({
  name: 'FocusItem',
  props: {},
  components:{
    FocusItemContent,
    FocusItemFolder,
    SelectedItemContent
  },
  data () {
    return {}
  },
  methods: {
  },
  computed: {
    ...mapState(
      {
        focusedItem: state => state.ui.focusedItem,
        selectedItems:state=>state.ui.selectedItems
      }),
  },
  setup () {
    return {
    }
  }
})
</script>

