<template>
  <div class="content-info">
    <div style="margin-bottom: 20px;height: auto;position: relative">
      <a-image v-if="['img','url'].indexOf(itemData.type)>-1"
               :width="200" style="max-height: 300px"
               :src="coverImg">
        <template #placeholder>
          <a-image
              src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200"
              :width="200"
              :preview="false"
          />
        </template>
      </a-image>
      <div v-else-if="itemData.type==='video'">

      </div>
      <a-image v-else :style="{objectFit:'cover',padding:20+'px'}" :width="200" :height="190"
               :preview="false"
               class=""
               :src="coverImg">
      </a-image>
      <img v-if="itemData.extData.favicon" onerror="this.src='./img/default.svg'" :src="itemData.extData.favicon"
           class="favicon" style="bottom: -10px;right: -10px">
      <div v-show="itemData.type==='video'" id="mse"></div>
    </div>
    <div style="margin-bottom: 10px">
      <a-textarea @blur="rename()" v-model:value="userInput.name" :autSize="{ minRows: 1, maxRows: 5 }"
                  style="margin-bottom: 10px" placeholder=""/>
      <!--      <a-select-->
      <!--        :options="options"-->
      <!--        mode="tags"-->
      <!--        placeholder=""-->
      <!--        style="width: 100%"-->
      <!--      ></a-select>-->
      <a-textarea @blur="editRemark()" style="margin-bottom: 10px" v-model:value="userInput.remark"
                  placeholder=""></a-textarea>
      <a-input @blur="editHref()" style="margin-bottom: 0" v-model:value="userInput.href" placeholder="">
        <template #suffix>
          <ExportOutlined title="" @click="ipc.addTab(itemData.meta.href)"/>
        </template>
      </a-input>
      <a-rate @change="setRate" v-model:value="userInput.rate" style="margin-bottom: 10px" :value="4" allow-half/>
      <!--      <a-select-->
      <!--        :options="options"-->
      <!--        mode="tags"-->
      <!--        placeholder=""-->
      <!--        style="width: 100%"-->
      <!--      ></a-select>-->
    </div>
    <div style="margin-bottom: 10px">
      <h4></h4>
      <div>
        {{ this.dateStr(itemData.baseInfo.createTime) }}
        <br>
        {{ this.dateStr(itemData.baseInfo.addTime) }}
        <br>
        {{ this.dateStr(itemData.baseInfo.modifyTime) }}
        <br>
        <br><span title=""
                       @click="showItem(itemData)"
                       style="color: #999;cursor:pointer;">{{
          itemData.baseInfo.relativePath
        }}{{ itemData.baseInfo.filename }}</span>
        <br/>
        {{ itemData.baseInfo.sizeText }}
      </div>
    </div>
    <div>
      <a-button @click="exportContent(itemData)" block></a-button>
    </div>
  </div>

</template>

<script>
import { ref, defineComponent } from 'vue'
import { ExportOutlined } from '@ant-design/icons-vue'
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
import contentModel from '@/utils/model/contentModel'
import { dateStr } from '@/utils/time'
import statusReturn from '@/utils/statusReturn'
import fileModel from '@/utils/model/fileModel'
import pathTools from '@/utils/pathTools'
import contentTypeHelper from '@/utils/contentTypeHelper'
import pathTool from '@/utils/pathTools'
import Player from 'xgplayer/dist/simple_player';
export default defineComponent({
  name: 'FocusItemContent',
  props: {},
  components: {
    ExportOutlined
  },
  data () {
    return {
      ipc,
      value3: ref(['a1', 'b2']),
      options: [
        { value: '' },
        { value: '' },
        { value: '' },
        { value: '' },
      ],
      userInput: {
        name: '',//
        remark: '',//
        rate: 0,//
        href: '',//
      },
      coverImg: '', //
    }
  },
  mounted () {
    this.userInput.name = this.itemData.meta.name
    this.userInput.remark = this.itemData.meta.remark || ''
    this.userInput.rate = this.itemData.meta.rate || 0
    this.userInput.href = this.itemData.meta.href || ''
    this.coverImg = this.cover()
    window.$updateFocusedItemInfo=this.updateItemInfo
    console.log('',this.updateItemInfo)
    if(this.itemData.type==='video')
    window.$xgplayer = new Player({
      id: 'mse',
      url: 'file://'+pathTool.join(this.itemData.baseInfo.path,this.itemData.baseInfo.filename),
      fitVideoSize: 'fixWidth',
      fluid: true,
      videoInit: true
    })


  },
  methods: {
    updateItemInfo(){
        let item=this.itemData
        let meta =item.meta
        this.userInput.name =meta.name || ''
        this.userInput.rate = meta.rate || 0
        this.userInput.href = meta.href || ''
        this.userInput.remark =meta.remark || ''
        if(item.type){
          if(item.type==='video'){
            if(window.$xgplayer){
              window.$xgplayer.once('destroy',()=>{
                this.$nextTick(()=>{
                  window.$xgplayer = new Player({
                    id: 'mse',
                    url: 'file://'+pathTool.join(item.baseInfo.path,item.baseInfo.filename),
                    fitVideoSize: 'fixWidth',
                    fluid: true,
                    videoInit: true
                  })
                })
                // setTimeout(()=>{
                //   window.$xgplayer = new Player({
                //     id: 'mse',
                //     url: 'file://'+pathTool.join(item.baseInfo.path,item.baseInfo.filename),
                //     fitVideoSize: 'fixWidth',
                //     fluid: true,
                //     videoInit: true
                //   })
                // },100)

              })
              window.$xgplayer.destroy()

            }
            //window.$xgplayer.reload()
          }else{
            this.coverImg=this.cover()
          }
        }
    },
    showItem(item){
      ipc.showItemInFolder(pathTool.join(item.baseInfo.path,item.baseInfo.filename))
    },
    dateStr,
    exportContent (content) {
      let path = pathTools.getStorePathFromRelativePath(content.meta.relative_path_full)
      fileModel.saveAs(path)
    },
    /**
     * 
     */
    rename () {
      if (this.userInput.name === this.itemData.name || this.userInput.name === '') {
        this.userInput.name = this.itemData.name
        return
      }
      let renameResult = contentModel.rename(this.itemData.meta.uuid, this.userInput.name)
      if (renameResult.status === statusReturn.successCode) {
        this.$message.success('')
        this.$store.commit('content/updateContentWithUuid',
            {
              uuid: this.itemData.meta.uuid,
              meta: renameResult.data
            })
      } else {
        this.userInput.name = this.itemData.meta.name
        this.$message.error('' + renameResult.info)
      }
    },
    editRemark () {
      if (this.userInput.remark === this.itemData.meta.remark) {
        return
      }
      let remarkResult = contentModel.update(this.itemData.meta.uuid, { remark: this.userInput.remark })
      if (remarkResult.status === statusReturn.successCode) {
        //this.$message.success('')
        this.$store.commit('content/updateContentWithUuid',
            {
              uuid: this.itemData.meta.uuid,
              meta: remarkResult.data
            })
      } else {
        this.userInput.remark = this.itemData.meta.remark
        this.$message.error('' + remarkResult.info)
      }
    },
    editHref () {
      if (this.userInput.href === this.itemData.meta.href) {
        return
      }
      let result = contentModel.update(this.itemData.meta.uuid, { href: this.userInput.href })
      if (result.status === statusReturn.successCode) {
        //this.$message.success('')
        this.$store.commit('content/updateContentWithUuid',
            {
              uuid: this.itemData.meta.uuid,
              meta: result.data
            })
      } else {
        this.userInput.href = this.itemData.meta.href
        this.$message.error('' + result.info)
      }
    },
    setRate () {
      let rateResult = contentModel.update(this.itemData.meta.uuid, { rate: this.userInput.rate })
      this.$store.commit('content/updateContentWithUuid',
          {
            uuid: this.itemData.meta.uuid,
            meta: rateResult.data
          })
    },
    cover () {
      if (!this.itemData){
        return
      }
      if (['img', 'url'].indexOf(this.itemData.type) > -1) {
        if (this.itemData.cover) {
          if (this.itemData.cover.startsWith('/')) {
            return 'file://' + this.$store.state.config.storePath + this.itemData.cover
          } else {
            return this.itemData.cover
          }
        } else {
          return ''
        }
      }
      return contentTypeHelper.getContentCover(this.itemData.ext)
    }
  },
  computed: {
    ...mapState(
        {
          itemData: state => state.ui.focusedItem.data,
          focusedItem: state=>state.ui.focusedItem,
          focusedItemUuid:state=>state.ui.focusedItemUuid,
        }),

  },
  setup () {
    return {
      random: ref(),
    }
  }
})
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="text/css" scoped>
.content-info {
  padding: 15px;
  background: #f1f1f1;
}

input, textarea {
  margin-bottom: 10px;
}

.ant-select {
  margin-bottom: 10px;
}
</style>
