
const fileHelper = {
    ignore:['.DS_Store', '_assets', '_db.json','_index.json'],
    /**
     * 
     * @param dir
     * @param options
     * @returns {{tree: *[]}}
     */
    getFileTree(dir, options) {
        const libPath=window.configDb.getStorePath()
         options=Object.assign( {
             showFile: true,//
             gotFlat: true,//
             filters: fileHelper.ignore,
             mapCallback:null,//
             relative:false//
         },options)
        let path = eval('require')('path');
        let fs = eval('require')('fs');
        var tree = []
        let flat = []//
        //let cur = 0
        // hash
        var mapDeep = {}
        mapDeep[dir] = 0
        // 
        // function getMap(dir, curIndex) {
        //     var files = fs.readdirSync(dir) //
        //     files.map(function (file) {
        //         //var subPath = path.resolve(dir, file) //
        //         var subPath = path.join(dir, file) //
        //         var stats = fs.statSync(subPath) //
        //         // filters
        //         if (options.filters.indexOf(file)===-1) {
        //             mapDeep[file] = curIndex + 1
        //             if (stats.isDirectory()) { //
        //                 return getMap(subPath, mapDeep[file]) //
        //             }
        //         }
        //     })
        // }
        // if(options.showDeep){
        //     getMap(dir, mapDeep[dir])
        // }

        function readDirs(dir) {
            var result = { //
                path: dir,
                title: path.basename(dir),
                type: 'dir',
            }
            var files = fs.readdirSync(dir) //
            result.children = files.map(function (file) {
                if(options.filters.indexOf(file)>-1) return
                if(options.mapCallback){
                    options.mapCallback(dir,file)
                }
                //var subPath = path.resolve(dir, file) //
                var subPath = path.join(dir, file) //
                var stats = fs.statSync(subPath) //
                if (stats.isDirectory() && !file.endsWith('.pxd')) { //
                    //.pxd
                    return readDirs(subPath, file, file) //
                }
                let fileInfo={ //
                    path: subPath,
                    name: file,
                    type: 'file',
                }
                if(options.relative){
                    fileInfo.relativePath=subPath.replace(libPath,'')
                }
                if(options.gotFlat){
                    flat.push(fileInfo)
                    if(options.showFile){//
                        return fileInfo
                    }else{
                        return false
                    }
                }
            })
            let flatObject={
                path:result.path,
                name:result.title,
                type:result.type
            }
            if(options.relative){
                flatObject.relativePath=result.path.replace(libPath,'')
            }
            flat.push(flatObject)
            return result //
        }

        tree.push(readDirs(dir, dir))
        let result = {
            tree: tree
        }
        if (options.gotFlat) {
            result['flat'] = flat
        }
        return result
    }
}

export default fileHelper
