import pathTools from '@/utils/pathTools'
import subFolderModel from '@/utils/model/subFolderModel'

const fs=eval('require')('fs')
/**
 * 
 * @type {{mutations: {updateContents(*, *): void}, state: (function(): {contents: [{extData: {domain: string, pwd: string, username: string}, name: string, href: string, type: string}]}), getters: {}}}
 */
const subFolder = {
    namespaced: true,
    state: () => ({
        subFolders: [],//


        myAllFolders: [],//
        myAllFoldersFlat:[],//


        myAllFoldersTreeData: []
    }),
    mutations: {
        /**
         * uuid
         * @param state
         * @param uuid
         * @param content
         */
        updateSubFolderWithUuid(state,args){
            state.subFolders.forEach((ct)=>{
                if(ct.meta.uuid===args.uuid){
                    ct.name=args.meta.name
                    ct.meta=args.meta
                }
            })
        },
        /**
         * 
         * @param state
         * @param subFolders
         */
        setCurrentSubFolders(state, subFolders) {
            state.subFolders = subFolders
        },
        /**
         * 
         * @param state
         * @param folders
         */
        setMyAllFolders(state, folders) {
            state.myAllFolders = folders.subFolders
        },
        setMyAllFoldersFlat(state,value){
            state.myAllFoldersFlat=value
        },
        assignSubFolderMeta(state,meta){
            let foundSubFolder=null
            state.subFolders=  state.subFolders.filter((subFolder)=>{
                if(subFolder){
                    if(pathTools.convertToCommonRelativePath(subFolder.baseInfo.relativePath+subFolder.baseInfo.filename)===meta.relative_path_full){
                       let tmp=subFolderModel.getSubFolderFromFolder(meta.relative_path_full)
                        if(!tmp){
                            return false
                        }
                    subFolder=tmp
                        foundSubFolder=subFolder
                    }
                    return true
                }

            })
            if(!foundSubFolder ){
                let tmp=subFolderModel.getSubFolderFromFolder(meta.relative_path_full)
                if(tmp)
                    state.subFolders.unshift(tmp)
            }
        },
    },
    getters: {
        getCurrentSubFolders(state) {
            return state.subFolders
        },
        getMyAllFoldersTreeData(state){
            return state
        }
    },
    actions: {
        updateTreeData(context) {
            let myAllFoldersFlat=[]
            //let myAllFoldersTreeData=[]
            let folders = []
            const storePath=context.rootState.config.storePath
            function readSubFolders(path,dir){
                dir.subFolders=[]
                let fsDirs=  fs.readdirSync(path).filter((file)=>{return (fs.statSync(path+'/'+file).isDirectory() && !file.startsWith('.DS'))})
                fsDirs.forEach(fsDir=>{
                    if(fsDir==='_assets'){
                        return
                    }
                    let subDir={}
                    subDir.path=(path+'/'+fsDir).replace('//','/')
                    subDir.relativePath=subDir.path.replace(storePath,'')
                    subDir.dirname=fsDir
                    subDir.title=fsDir

                    //
                    subDir.key=subDir.relativePath
                    subDir.parentDir=dir.relativePath
                    subDir.children=subDir.subFolders = readSubFolders(subDir.path,subDir)
                    subDir.contentCount=fs.readdirSync(subDir.path).length>0?fs.readdirSync(subDir.path).length:0




                    dir.subFolders.push(subDir)
                    myAllFoldersFlat.push(subDir)
                })
                return dir.subFolders
            }
            folders.path=storePath
            folders.relativePath='/'
            folders.dirname=storePath.substr(storePath.replace('\\','/').lastIndexOf('/'))
           // let rootSubFolders=fs.readdirSync(storePath).map((file)=>{return fs.statSync(storePath+file).isDirectory()})
            folders.subFolders=readSubFolders(folders.path,folders)
            myAllFoldersFlat.push(folders)
            context.commit('setMyAllFolders', folders)
            context.commit('setMyAllFoldersFlat',myAllFoldersFlat)
        }
    }
}

export default subFolder
