<template>
  <div>
    <div style="height: 54px">
      <ArrowLeftOutlined class="action-icon" @click="this.$router.back()"/> 
      <div style="float:right;width: 300px;display: flex">
        <div style="width: 200px">
<!--          <a-slider></a-slider>-->
        </div>
        <div style="flex:1;">
          <a href="tsb://app/redirect/?package=com.thisky.import&url=/" title=""><ImportOutlined class="action-icon" /></a>
          <EditOutlined class="action-icon"/>
        </div>
      </div>
    </div>
    <div style="padding: 15px">
      <a-table
          :columns="columns"
          :dataSource="dataSource"
          :pagination="false"
          :rowClassName="(_record, index) => (index % 2 === 1 ? 'table-striped' : null)" :size="size"
          bordered
          class="ant-table-striped"
          rowKey="id"
      >
        <template #bodyCell="{ column, text, record }">
          <!--          <template v-if="column.dataIndex==='startText'">-->
          <!--            <a-star></a-star>-->
          <!--          </template>  -->
          <template v-if="['title', 'username', 'pwdText'].includes(column.dataIndex)">
            <div>
              <a-input
                  v-if="editableData[record.key]"
                  v-model:value="editableData[record.key][column.dataIndex]"
                  style="margin: -5px 0"
              />
              <template v-else>
                {{ text }}
              </template>
            </div>
          </template>
          <template v-else-if="column.dataIndex === 'action'">
            <div class="editable-row-operations">
          <span v-if="editableData[record.key]">
            <a @click="save(record.key)"></a>
            <a-popconfirm title="" @confirm="cancel(record.key)">
              <a></a>
            </a-popconfirm>
          </span>
              <span v-else>
            <a @click="edit(record.key)"></a>
          </span>
            </div>
          </template>
        </template>
      </a-table>
    </div>
  </div>
</template>

<script>
import ipc from "@/utils/ipc";
import {ArrowLeftOutlined,ImportOutlined} from "@ant-design/icons-vue"
import {cloneDeep} from 'lodash-es'

export default {
  name: "PasswordListPage",
  components: {
    ArrowLeftOutlined,ImportOutlined
  },
  methods: {
    edit(key) {
      this.editableData[key] = cloneDeep(this.dataSource.value.filter(item => key === item.key)[0]);
    },
    save(key) {
      Object.assign(this.dataSource.value.filter(item => key === item.key)[0], this.editableData[key]);
      delete this.editableData[key];
    },
    cancel(key) {
      delete this.editableData[key];
    }
  },
  data() {
    return {
      editableData: {},
      size: 'middle',
      //{domain: 's.apps.vip', username: '18205839158', password: 'chenyixiao1'}
      columns: [
        {
          title: '',
          dataIndex: 'id',
          key: 'id',
          width: 50
        },
        {
          title: '',
          dataIndex: 'domain',
          key: 'domain',
          sorter: true,
          width: 200
        },
        {
          title: '',
          dataIndex: 'title',
          key: 'title',
          sorter: true
        },
        {
          title: '',
          dataIndex: 'starText',
          key: 'starText',
          sorter: true,
          width: 100,
          filters: [
            {text: '', value: '3'},
            {text: '', value: '2'},
            {text: '', value: '1'}
          ],
        },
        {
          title: '',
          dataIndex: 'username',
          key: 'username',
          sorter: true
        },
        {
          title: '',
          dataIndex: 'pwdText',
          key: 'pwdText',
        },
        {
          title: '',
          dataIndex: 'action',
          key: 'action',
        }
      ],
      dataSource: []
    }
  }, created() {
    let that = this
    ipc.fetchAllPwds().then((pwds) => {
      let dataSource = []
      let i = 1
      dataSource = pwds.map((pwd) => {
        //todo 
        pwd.id = i
        i++
        pwd.pwdText = ""
        pwd.pwd = pwd.password
        //0pwd.starText = ''
        pwd.star = String(1)
        pwd.action = ''
        return pwd
      })
      that.dataSource = dataSource
    }).catch(e => {
      that.$message.error('' + e + '')
    })

  }
}
</script>

<style scoped>
.ant-table-striped :deep(.table-striped) td {
  background-color: #fafafa;
}
</style>
