function decodeBase64Image(dataString)
{
  var matches = dataString.match(/^data:([A-Za-z-+/]+);base64,(.+)$/);
  var response = {};

  if (matches.length !== 3)
  {
    return new Error('Invalid input string');
  }

  response.type = matches[1];
  response.data = new Buffer(matches[2], 'base64');

  return response;
}
// function convertBase64ToBlob(base64, type) {
//   var bytes = window.atob(base64);
//   var ab = new ArrayBuffer(bytes.length);
//   var ia = new Uint8Array(ab);
//   for (var i = 0; i < bytes.length; i++) {
//     ia[i] = bytes.charCodeAt(i);
//   }
//   return new Blob([ab], { type: type });
// }
const fs=eval('require')('fs')
const {clipboard,nativeImage} =eval('require')('electron')
const {dialog}=eval('require')('@electron/remote')
export default {
  name: 'ImageDetailPage',
  components: {
    ArrowLeftOutlined
  },
  data () {
    return {
      pathUri: '',
      path:'',
      instance: null,
    }
  },
  mounted () {
    this.path=this.$route.params['path']
    this.pathUri = 'file://' + this.path+'?t='+Date.now()
    this.init(this.pathUri)
  },

  beforeRouteUpdate (to,from) {
    console.log(to,from)
    console.log('')
    this.path=this.$route.params['path']
    this.pathUri = 'file://' + this.path
    this.init(this.pathUri)
  },
  methods: {
    leave(){
      Modal.confirm({
        title: '',
        content: '',
        okText: '',
        cancelText: '',
        onOk: () => {
          this.$router.back()
        }
      })
    },
    init (path) {
      this.instance = new ImageEditor(
          document.querySelector('#tui-image-editor'),
          {
            includeUI: {
              loadImage: {
                path: path,
                name: '',
              },
              initMenu: 'draw', // 
              menuBarPosition: 'bottom', // 
              theme: {
                'common.bi.image': '',
                'common.bisize.width': '0px',
                'common.bisize.height': '0px',
                'loadButton.display': 'none',
                'downloadButton.display': 'none',
              },//customTheme,
              locale: locale_zh,
            },
            initMenu: 'rotate',
            cssMaxWidth: 1000, // canvas 
            cssMaxHeight: 600, // canvas 

          }
      )
      return customTheme
      //document.getElementsByClassName("tui-image-editor-main")[0].style.top = "45px"; // 
    },
    copyToClip(){
      const image=nativeImage.createFromDataURL(this.getBase64Data())
      clipboard.writeImage(image)
      message.success('')
    },
    // 
    getBase64Data(){
      const base64String = this.instance.toDataURL() // base64 
      const data = window.atob(base64String.split(',')[1])
      const ia = new Uint8Array(data.length)
      for (let i = 0; i < data.length; i++) {
        ia[i] = data.charCodeAt(i)
      }
      return base64String
    },
    save () {
      Modal.confirm({
        title: '',
        content: '',
        okText: '',
        cancelText: '',
        onOk: () => {
          let imageBuffer=decodeBase64Image(this.getBase64Data())
          fs.writeFile(this.path,imageBuffer.data,(err)=>{
            if(err){
              console.warn(err)
              message.error('')
            }else{
              message.success('')
            }
          })
        }
      })
    },
    saveAs () {
      let oldFilename=this.path.replace('\\','/')
      let dir=this.path.substr(0,oldFilename.lastIndexOf('/'))
      let newFilename=dir+'/'+this.path.substring(this.path.lastIndexOf('/'),this.path.lastIndexOf('.'))+'_'+this.path.substr(this.path.lastIndexOf('.'))
       let path= dialog.showSaveDialogSync({
          title:'',
          defaultPath:newFilename
        })
      if(path){
        let imageBuffer=decodeBase64Image(this.getBase64Data())
        fs.writeFile(path,imageBuffer.data,(err)=>{
          if(err){
            console.warn(err)
            message.error('')
          }else{
            message.success('')
          }
        })
      }
    }
  }
}
</script>

