const statusReturn={
  successCode:1,
  failureCode:0,
  standardReturn(status,info,data={}){
    return {
      status:status,
      info:info,
      data:data,
      isFailure:status===this.failureCode,
      isSuccess:status===this.successCode
    }
  },
  success(info,data={}){
    return statusReturn.standardReturn(statusReturn.successCode,info,data)
  },
  failure(info,data={}){
    return statusReturn.standardReturn(statusReturn.failureCode,info,data)
  }
}

export default statusReturn

