          <a-dropdown v-model:visible="visibleSearchHistory" >
          <a-input
              v-model:value="searchWords"
              placeholder=""
              style="width: 200px"
              @focus="onFocus"
              spellcheck="false"
              @change="onSearch"
              @blur="addHistory"
          >
            <template #prefix>
              <a-dropdown v-model:visible="searchTypeVisible">
                <template #overlay>
                  <a-menu @click="handleSearchTypeMenuClick">
                    <a-menu-item   key="name">
                      <a-checkbox v-model:checked="searchType.name"  >
                        
                      </a-checkbox>
                    </a-menu-item>
                    <a-menu-item  key="remark">
                      <a-checkbox  v-model:checked="searchType.remark" >
                        
                      </a-checkbox>
                    </a-menu-item>
                    <a-menu-item  key="href">
                      <a-checkbox   v-model:checked="searchType.href">
                        
                      </a-checkbox>
                    </a-menu-item>
                  </a-menu>
                </template>
                <span>
                  <search-outlined />
                  <DownOutlined />
                </span>
              </a-dropdown>
          </template>
          </a-input>
            <template #overlay>
              <a-menu @click="setSearchWords">
                <a-menu-item :disabled="true">
                  
                </a-menu-item>
                <a-menu-item v-for="history in filteredSearchHistory"   :key="history">
                  {{ history }}
                </a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
        </div>
      </div>
      </template>
    </a-page-header>
    <div v-if="searchWords!==''" style="color: #999;font-size: 12px;margin-left: 20px">
      <strong style="color: #f81f1f">{{searchWords}}</strong>
      <br>
      
      <a-checkable-tag v-model:checked="searchCurrent">"{{ folder.all.name===''?'': folder.all.name }}"</a-checkable-tag>
      <a-checkable-tag v-show="folder.all.relativeFullPath!==''" v-model:checked="searchRoot" >""</a-checkable-tag>
      <a-checkbox v-show="!searchRoot" style="font-size: 12px;margin-left: 10px" v-model:checked="searchSubFolder"></a-checkbox>
    </div>
    <div id="refreshBtnWrapper" style="position: fixed;text-align: center" :style="{right:Number(dividerPos.right+20)+'px'}">
      <a-button title="" @click="refresh()" id="refreshingBtn"  :loading="refreshing" type="primary" shape="circle" style="text-align: center">
        <template #icon>
          <RedoOutlined  />
        </template>
      </a-button>
    </div>
  </div>
</template>

<script>
import {RedoOutlined, LoginOutlined,DownOutlined ,SearchOutlined} from '@ant-design/icons-vue';// BorderOuterOutlined,
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
export default {
  name: 'ContentHeader',
  props: {
  },
  components:{
    //BorderOuterOutlined,
    RedoOutlined,
    LoginOutlined,DownOutlined,SearchOutlined
  },
  data() {
    return {
      searchTypeVisible:false,
      searchCurrent:true,//
      visibleSearchHistory:false,
      searchWords:'',
      searchHistory:[],
      filteredSearchHistory:[],
      searchType: {
        name:true,
        remark:true,
        href:true
      },
      refreshing:false,
      routes: [
        {
          path: 'index',
          breadcrumbName: 'First-level Menu',
        },
        {
          path: 'first',
          breadcrumbName: 'Second-level Menu',
        },
        {
          path: 'second',
          breadcrumbName: 'Third-level Menu',
        },
      ]
    }
  },

  mounted(){
    this.$store.commit('ui/setItemSize',Number(localStorage.getItem('itemSize')))
    let searchHistoryString=localStorage.getItem('searchHistory') || '[]'
    this.searchHistory=JSON.parse(searchHistoryString)
    this.filteredSearchHistory=this.searchHistory.slice(0,8)
  },

  methods:{
    handleSearchTypeMenuClick(){
    },
    setSearchWords(input){
      this.searchWords=input.key
      this.doSearch()
    },
    async doSearch(){
      this.$store.commit('ui/setSearcher',{
        words:this.searchWords,
        type:this.searchType,
        searchSubFolder: true,
        searchRoot:false
      })
    },
    onFocus(event){
      setTimeout(()=>{
        event.target.select()
      },200)
    },
    async onSearch(input){
      let value=input.target.value
      if(value===''){
        this.filteredSearchHistory=this.searchHistory.slice(0,8)
        this.doSearch()
        return
      }
      this.visibleSearchHistory=true
      this.filterSearchHistory(value)
      this.doSearch()
    },
    async filterSearchHistory(value){
      if(value===''){
        return this.filteredSearchHistory=this.searchHistory.slice(0,8)
      }
      this.filteredSearchHistory=this.searchHistory.filter(record=>{
        return record.toLowerCase().indexOf(value.toLowerCase())>-1
      })
      this.filteredSearchHistory= this.filteredSearchHistory.slice(0,8)
      if(this.filteredSearchHistory.length===0){
        this.visibleSearchHistory=false
      }
    },
    addHistory(input){
      let value=input.target.value
      if(value==='') return
      let exists=this.searchHistory.find((v)=>{
        return v===value
      })
      if(!exists)
      {
        this.searchHistory.push(value)
        localStorage.setItem('searchHistory',JSON.stringify(this.searchHistory))
      }
    },
    refresh(){
      this.refreshing=true
      this.$store.dispatch('ui/reload',()=>{
        this.refreshing=false
      })
      document.getElementById('refreshingBtn').classList.add('refreshing')
      setTimeout(()=>{
        document.getElementById('refreshingBtn').classList.remove('refreshing')
      },500)
    },
    openDir(dir){
      ipc.openDir(dir)
    },
    changeTab(){
      this.refreshing=true
      this.$store.dispatch('ui/changeTab',{name:'all'})
      setTimeout(()=>this.refreshing=false,500)
    }
  },
  watch:{
    searchCurrent(newValue){
      this.searchRoot=!newValue
    }
  },
  computed:{
    itemSize: {
      get() {
        return this.$store.state.ui.itemSize
      },
      set(newValue) {
       this.$store.commit('ui/setItemSize',newValue)
        localStorage.setItem('itemSize',newValue)
      }
    },
    searchSubFolder:{
      get() {
        return this.$store.state.ui.searcher.searchSubFolder
      },
      set(newValue) {
        this.$store.commit('ui/setSearcher',Object.assign(this.$store.state.ui.searcher,{searchSubFolder:newValue}))
      }
    },
    searchRoot:{
      get(){
        return this.$store.state.ui.searcher.searchRoot
      },
      set(newValue){
        this.$store.commit('ui/setSearcher',Object.assign(this.$store.state.ui.searcher,{searchRoot:newValue}))
        this.searchCurrent=!newValue
      }

    },
    ...mapState({
      isApp:state=>state.config.isApp,
      currentFolder:state=>state.ui.currentFolder,
      currentTab:state=>state.ui.currentTab,
      openHistory:state=>state.ui.openHistory,
      dividerPos:state=>state.ui.dividerPos,
      folder:state=>state.ui.currentFolder,

    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@keyframes rotate{
  0%{
    transform: rotate(0);
  }
  100%{
    transform:rotate(365deg);
  }
}
.refreshing{
  transition: 0.2s;
  animation: rotate .5s linear infinite;  /*rotate*/
}
</style>
