<template>
  <template v-if="!isApp && userInfo.uid===0">
    <a-result status="403" title="403" sub-title="">
      <template #extra>
        <a-button type="primary" @click="goLogin()">
          
        </a-button>
        <!--//todo web-->
      </template>
    </a-result>
  </template>
  <a-layout   v-else>
    <div id="rightResizer" @mousedown="starDrag('right')"  style="z-index:999;position:fixed;top:0;height: 100vh;background: transparent;cursor:ew-resize;width: 5px;" :style="{right:(this.rightWidth-5)+'px'}">
    </div>
    <div id="leftResizer" :style="{left:this.leftWidth+'px'}" style="z-index:999;top:0; position:fixed;cursor:ew-resize;width: 5px;height: 100vh;background: transparent" @mousedown="starDrag('left')"></div>
    <a-layout-sider style="position: relative" id="left" class="wrapper-scroller" theme="light" :width="leftWidth"
                    :style="{ overflow: 'auto', height: '100vh', position: 'fixed', left: 0 ,background:'#f1f1f1'}">
      <SideBar/>

    </a-layout-sider>
    <a-layout class="wrapper-scroller"
              :style="{background:'white', marginLeft:this.leftWidth+ 'px',marginRight:this.rightWidth+'px',height:'100vh', position: 'fixed',width:'calc(100vw - '+String(Number(this.leftWidth)+Number(this.rightWidth))+'px)',overflowY:'auto' }">
      <router-view></router-view>
    </a-layout>
    <a-layout-sider id="right"  class="wrapper-scroller focused-panel" theme="light" :width="rightWidth">
      <FocusItem/>
    </a-layout-sider>
  </a-layout>
</template>

<script>
import SideBar from '@/components/SideBar'
import FocusItem from '@/components/FocusItem'
import { mapState } from 'vuex'

export default {
  name: 'MainPage',
  components: {
    SideBar,
    FocusItem
  },
  data () {
    return {
      isApp: window.isApp,
      resizingObject: null,
      resizing: false,
      leftWidth:240,
      rightWidth:240,
    }
  },
  computed: {
    ...mapState(
        {
          userInfo: state => state.user.userInfo,
          storePath: state => state.config.storePath
        }
    )
  },
  watch:{
    rightWidth:function(){
      this.updateRefreshBtnPos()
    }
  },
  mounted () {
    this.leftWidth=Number(localStorage.getItem('leftWidth')) || 240
    this.rightWidth=Number(localStorage.getItem('rightWidth')) || 240
    this.updateRefreshBtnPos()
    document.onmouseup=this.endDrag
    document.onmousemove=this.resizeSidebar
    if (window.isApp) {
      this.$store.commit('config/setIsApp', this.isApp) //
      this.$store.dispatch('ui/init')
      this.$store.dispatch('ui/changeTab', {
        name: 'all',
        alias: ''
      })
    }
  },
  methods: {
    updateRefreshBtnPos(){
      let right=document.getElementById('refreshBtnWrapper')
      if(right) right.style.right=this.rightWidth+20 +'px'
    },
    starDrag (id) {
      this.resizing = true
      this.resizingObject = id
    },
    resizeSidebar (event) {
      let min=240
      let max=480
      if(!this.resizing){
        return
      }
      if(this.resizingObject==='left'){
        if(event.clientX<min){
          this.leftWidth=min
          return
        }else if(event.clientX>max){
          this.leftWidth=max
          return
        }
        this.leftWidth=event.clientX
        this.leftWidth=event.clientX
      }else if(this.resizingObject==='right'){
        if(document.body.offsetWidth-event.clientX-5<min){
          this.rightWidth=min
          return
        }else if(document.body.offsetWidth-event.clientX-5>max){
          this.rightWidth=max
          return
        }
        this.rightWidth=document.body.offsetWidth-event.clientX-5
      }
    },
    endDrag () {
      this.resizing = false
      this.$store.commit('ui/setDividerPos',{left:this.leftWidth,right:this.rightWidth})
      localStorage.setItem('leftWidth',this.leftWidth)
      localStorage.setItem('rightWidth',this.rightWidth)

    },
    goLogin () {
      location.href = this.$store.state.config.server.loginUrl
    }
  }
}
</script>

<style scoped>
.ant-layout-sider-children{
  position: relative;
}
.focused-panel{
  position: fixed;overflow: auto;right: 0;background: #f1f1f1;height: 100vh
}
</style>
