import { createApp } from 'vue'
import Antd from 'ant-design-vue'
import App from './App.vue'
import './assets/css/main.css'
import 'ant-design-vue/dist/antd.css'
import store from './store/index'
import tsbk from '@/core/tsbSdk'
import routes from '@/pages/route'
import {createRouter,createWebHashHistory} from 'vue-router'
//import 'ant-design-vue/dist/antd.dark.css'
const router=createRouter({
  history:createWebHashHistory(),
  routes
})

let app=createApp(App).use(router).use(Antd).use(store).mount('#app')
if(window.tsbSdk){
  //
  window.tsbk=tsbk //sdkwindows
  tsbk.config({
    signature: "ts"
  })
  tsbk.on('gotUserInfo',(event,args)=> {
    let user =args.user.value
    app.$store.commit('user/setUserInfo',user)
  })
  tsbk.ready()
  window.appReady()
}else{
  //
}
window.app=app
