<template>
  <div v-if="subFolders.length>0 && searcher.words===''">
    <h3 style="margin-bottom:10px;">
      <span style="cursor: pointer" @click="toggleShowFolderList">{{ subFolders.length }}
      <caret-down-outlined v-show="showFolderList"/>
        <caret-right-outlined v-show="!showFolderList"/>
      </span>
    </h3>
    <div v-show="showFolderList" style="min-height:150px">
      <a-dropdown :trigger="['contextmenu']" v-for="folder in subFolders" v-bind:key="folder" >
        <div  class="sub-folder-wrapper" @click="selectItem(folder)" @dblclick="changePath(folder.name)"
             :style="{width:itemSize+'px',height:itemSize+50+'px'}" style="">
          <div :class="{hover:folder.hover}" @dragenter="onDragEnter($event,folder)"
               @dragleave="onDragLeave($event,folder)"
               @dragover="onDragOver($event,folder)"
               @drop="onDrop($event,folder)" class="cover-wrapper" style="position: relative"
               :style="{width:itemSize+'px',height:itemSize+'px'}">
            <div class="sub-folder-frame" :style="{width:itemSize+'px',height:itemSize+'px'}"></div>
            <div v-if="folder.meta.new" class="type-label new-label">New</div>
            <a-image style="" :style="{width:itemSize+'px',height:itemSize+'px'}" :preview="false"
                     class="sub-folder-cover"
                     :class="{'selected':typeof this.focusItem!=='undefined' && this.focusItem.data===folder}"
                     :src="folder.cover">
            </a-image>
          </div>
          <div class="folder-name">{{ folder.name }}</div>
          <div class="inner-content-count">{{ folder.contentsCount }} </div>
        </div>
        <template #overlay>
          <a-menu>
            <a-menu-item key="openPath" @click="open(folder.baseInfo.path+'/'+folder.baseInfo.filename)"></a-menu-item>
            <a-menu-item @click="renameFolder(folder.baseInfo.path+'/'+folder.baseInfo.filename,folder.baseInfo.filename)" key="rename"></a-menu-item>
            <a-menu-item key="find" @click="showItemInFolder(folder.baseInfo.path+'/'+folder.baseInfo.filename)">
            </a-menu-item>
            <a-menu-item key="delete" @click="trashItem(folder)">
            </a-menu-item>
          </a-menu>
        </template>
      </a-dropdown>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
import { CaretDownOutlined, CaretRightOutlined } from '@ant-design/icons-vue'
import pathTools from '@/utils/pathTools'
export default {
  name: 'SubFolderList',
  props: {
    msg: String
  },
  components: {
    CaretDownOutlined, CaretRightOutlined
  },
  data () {
    return {
      showFolderList: true,
      askModelVisible: false,
      waitingMoveList: [],//
      movedContentCount: 0,//
      failureContentCount: 0,//
      askingContent: {},//
      targetFolder: null,
      subFolderHandler: window.app.$refs['subFolderHandler']
    }
  },
  computed: {
    ...mapState({
      subFolders: state => state.subFolder.subFolders,
      itemSize: state => state.ui.itemSize,
      focusItem: state => state.ui.focusedItem,
      searcher:state=>state.ui.searcher
    })
  },
  methods: {
    /**
     * 
     * @param storePath
     * @param dirname
     */
    renameFolder(storePath,dirname){
      let relativePath=pathTools.getRelativeFullPathFromFullPath(storePath).replaceAll('//','/')
      this.subFolderHandler.renameFolder(relativePath,dirname)
    },
    onDragEnter (event, folder) {
      folder.hover = true
    },
    onDragLeave (event, folder) {
      folder.hover = false
    },
    onDragOver (event, folder) {
      if (!folder.hover) {
        folder.hover = true
      }
      event.preventDefault()
    },
    onDrop (event, folder) {
      window.app.$refs['subFolderHandler'].startMoveContents(this.$store.state.ui.selectedItems,folder,()=>{folder.hover = false})
    },

    toggleShowFolderList () {
      this.showFolderList = !this.showFolderList
    },
    changePath (path) {
      this.$store.dispatch('ui/changeFolder', { path: this.$store.state.ui.currentFolder.path + path + '/' })
    },
    selectItem (folder) {
      this.$store.commit('ui/focusItem', {
        type: 'folder',
        data: folder
      })
    },
    open: ipc.openExternal,
    showItemInFolder: ipc.showItemInFolder,
    trashItem (folder) {
      let path= pathTools.join(folder.baseInfo.path,folder.baseInfo.filename)
      window.app.$refs['subFolderHandler'].trashItem(path)
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.ant-modal-footer {
  text-align: left;
}
</style>
