<template>
  <div @dblclick="openDetail(content)" @click="setSelected" class="cover-wrapper" style="position: relative" :style="{width:itemSize+'px',height:itemSize+'px'}">
    <div class="type-label"></div>
    <PhotoCard @onOver="zindex=999" @onLeave="zindex=1" style="background: #f1f1f1" :style="{'z-index':zindex,width:itemSize+'px',height:itemSize+'px'}"  class="content-cover" :img="cover" link=""></PhotoCard>


<!--    <div class=" type-label preview" @click.stop=""><zoom-in-outlined /></div>-->
  </div>
  <div class="folder-name" style="position: relative">{{ content.name }}</div>
  <div class="ext"><span >{{ content.ext.substr(content.ext.lastIndexOf('.')+1) }}</span> {{ content.extData.width }} X {{ content.extData.height }}</div>
</template>

<script>
import { mapState } from 'vuex'
import contentTypeHelper from "@/utils/contentTypeHelper";
import PhotoCard from '@/components/content/type/PhotoCard'
export default {
  name: 'TypeImg',
  emits:['setSelected','openDetail'],
  components:{
    PhotoCard
  },
  props: {
    content: Object
  },
  data(){
    return {
      debounce:null,
      contentTypeHelper,
      zindex:1
    }
  },
  computed: {
    ...mapState(
      {
        itemSize: state => state.ui.itemSize
      },

    ),
    cover(){
      if(this.content.cover.startsWith('/')){
        return 'file://'+this.$store.state.config.storePath+this.content.cover+'?t='+Date.now()
      }else{
        return this.content.cover
      }
    }
  },
  methods:{
    over() {
      const refl = this.$refs.refl;
      refl.style.opacity = 1;
    },
    leave() {
      const card = this.$refs.card;
      const refl = this.$refs.refl;
      card.style.transform = `perspective(500px) scale(1)`;
      refl.style.opacity = 0;
    },

    move() {
      const card = this.$refs.card;
      const refl = this.$refs.refl;

      const relX = (event.offsetX + 1) / card.offsetWidth;
      const relY = (event.offsetY + 1) / card.offsetHeight;
      const rotY = `rotateY(${(relX - 0.5) * 60}deg)`;
      const rotX = `rotateX(${(relY - 0.5) * -60}deg)`;
      card.style.transform = `perspective(500px) scale(2) ${rotY} ${rotX}`;

      const lightX = this.scale(relX, 0, 1, 150, -50);
      const lightY = this.scale(relY, 0, 1, 30, -100);
      const lightConstrain = Math.min(Math.max(relY, 0.3), 0.7);
      const lightOpacity = this.scale(lightConstrain, 0.3, 1, 1, 0) * 255;
      const lightShade = `rgba(${lightOpacity}, ${lightOpacity}, ${lightOpacity}, 1)`;
      const lightShadeBlack = `rgba(0, 0, 0, 1)`;
      refl.style.backgroundImage = `radial-gradient(circle at ${lightX}% ${lightY}%, ${lightShade} 20%, ${lightShadeBlack})`;
    },
    scale: (val, inMin, inMax, outMin, outMax) =>
    {
      return outMin + (val - inMin) * (outMax - outMin) / (inMax - inMin)
    },

    openDetail(content){
      this.$emit('openDetail',content)
    },
    setSelected(){
      this.$emit('setSelected')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

