<template>
  <a-row>
    <a-col flex="220px"
           class="wrapper-scroller"
           style="height: 100vh;overflow: auto;background-color:#f1f1f1;padding:10px;box-shadow: 0 0 8px rgba(65,65,65,0.49)">
      <div>
        <div class="input" style="height: 200px;position:relative;background: rgba(0,0,0,0.23);border-radius: 6px;">
          <a-image :height="200"
                   :width="200"
                   :src="addContentInfo.cover"
                   style=" object-fit: cover;
  height: 200px !important;
  border-radius: 6px;
  box-shadow: 0 0 8px rgba(73, 73, 73, 0.4)"
          >
          </a-image>
          <img :src="addContentInfo.extData.favicon" class="favicon">
        </div>
      </div>
      <div>
        <a-textarea class="input" placeholder="" v-model:value="addContentInfo.name"></a-textarea>
      </div>
      <div>
        <a-textarea class="input" placeholder="" v-model:value="addContentInfo.href"></a-textarea>
      </div>
      <!--      <div>-->
      <!--        <a-textarea class="input" place-holder=""></a-textarea>-->
      <!--      </div>-->
            <div>
              <a-textarea class="input" placeholder="" v-model:value="addContentInfo.remark"></a-textarea>
            </div>
      <div>
        <a-rate v-model:value="addContentInfo.rate" style="margin-bottom: 10px" :value="0" allow-half/>
      </div>
<!--      <a-select-->
<!--          v-model:value="value"-->
<!--          mode="tags"-->
<!--          style="width: 100%"-->
<!--          placeholder=""-->
<!--          :options="options"-->
<!--          @change="handleChange"-->
<!--      ></a-select>-->
    </a-col>
    <a-col flex="auto" style="padding: 10px;width: 260px">
      <div style="padding-bottom: 10px">
        <a-input-search
            v-model:value="searchFolderValue"
            placeholder=""
            style="width: 100%"
        />
      </div>
      <a-tabs style="width: 260px" size="small" centered :animated="false">
        <!--        <a-tab-pane key="often" tab="" style="padding:0 !important;">-->
        <!--          <div class="wrapper-scroller" style="height: calc(100vh - 135px );overflow: auto">-->
        <!--          </div>-->
        <!--        </a-tab-pane>-->
        <a-tab-pane key="my" tab="" style="padding:0 !important;">
          <div class="wrapper-scroller" style="height: calc(100vh - 135px );overflow: auto">
            <TreeList :searchValue="searchFolderValue" :flatData="userFlatFolders" @onSelect="setSelectedFolder" :treeData="userFolders"></TreeList>
          </div>
        </a-tab-pane>
        <!--        <a-tab-pane key="group" tab="" style="padding:0 !important;">-->
        <!--          <div class="wrapper-scroller" style="height: calc(100vh - 135px );overflow: auto">-->
        <!--          </div>-->
        <!--        </a-tab-pane>-->
      </a-tabs>

      <div style="position: fixed;bottom: 0;padding-bottom: 5px;font-size: 12px;color: #999;max-width: 300px">
        <div v-if="selectedFolder">
          /{{ selectedFolder }}
        </div>
        <div v-else></div>
      </div>
      <a-button @click="save" style="position:fixed;right: 10px;" size="small" type="primary"></a-button>
    </a-col>
  </a-row>

</template>

<script>
import {mapState} from "vuex";
import TreeList from "@/components/TreeList";
import ipc from "@/utils/ipc";
import contentModel from "@/utils/model/contentModel";
import pathTools from "@/utils/pathTools";
import {Modal} from "ant-design-vue";

export default {
  name: "PopSaveToFolderPage",
  data() {
    return {
      selectedFolder: '',
      searchFolderValue:'',
      options: [{
        value: 'toRead',
        label: '',
      }, {
        value: 'toDo',
        label: '',
      }, {
        value: 'other',
        label: '',
      }],
      addContentInfo: {
        name: '',
        href: '',
        cover:'',
        rate:0,
        remark:'',
        extData:{
        }
      },
    }
  },
  components: {
    TreeList
  },
  methods: {
    setSelectedFolder(selectedKeys) {
      this.selectedFolder = selectedKeys[0]
      //this.selectedFolder=selectedKeys
    },
    updateCapture(data) {
      this.addContentInfo.cover = 'file://' + data.url
    },
    countDown() {
      let secondsToGo = 3;
      const modal = Modal.success({
        title: '',
        content: `${secondsToGo}.`,
        okText:''
      });
      const interval = setInterval(() => {
        secondsToGo -= 1;
        modal.update({
          content: `${secondsToGo}.`,
        });
      }, 1000);
      setTimeout(() => {
        clearInterval(interval);
        modal.destroy();
        ipc.hideFavPop()
      }, secondsToGo * 1000);
    },
    save(){
      let filterNameFiltered=pathTools.filterFilename(this.addContentInfo.name)
      if(filterNameFiltered.hadMapped.length){
        //
        this.addContentInfo.name=filterNameFiltered.filename



        window.app.$message.error({content:
              `: ` + filterNameFiltered.hadMapped.join(' ') + ` `})
        return
      }
      let message=''
      let error=false
      if(this.addContentInfo.name.trim()==='' )
      {
        message=''
        error=true
      }
      if(this.addContentInfo.href.trim()===''){
        message=''
        error=true
      }
      if(error){
        window.app.$message.error({content:
              message})
        return
      }

      console.log('')
      window.libDb.reload()//

      if(contentModel.addLink(
          this.selectedFolder,
          this.addContentInfo.name,
          this.addContentInfo.href,
          this.addContentInfo.cover,
          this.addContentInfo.extData.favicon,
          this.addContentInfo.remark,
          this.addContentInfo.rate))
      {
        window.app.$store.dispatch('subFolder/updateTreeData')
        ipc.callAppReload()//
        ipc.message({type:'success',config:{content:''}})
        ipc.hideFavPop()
       // this.countDown()
      }
    }
  },
  mounted() {
    this.$store.dispatch('ui/init')
    ipc.bindAddPageInfoListener((info) => {
      let content={}
      content.name=pathTools.filterFilename(info.title).filename
      content.href=info.url
      content.remark=''
      content.extData={}
      if (info.favicon === null) {
        content.extData.favicon = ''
      }else{
        content.extData.favicon=info.favicon.url
      }
      this.addContentInfo = content
    }, this.updateCapture)
    window.getAddPageInfo = () => {
      ipc.getAddPageInfo()
    }//
    ipc.noticeAddPageReady()//
  },
  computed: {
    ...mapState(
        {
          userFolders: state => state.subFolder.myAllFolders,
          userFlatFolders:state=>state.subFolder.myAllFoldersFlat,
        }
    )
  }
}
</script>

<style scoped>
.input {
  margin-bottom: 10px;
}



</style>
